import { createSlice } from "@reduxjs/toolkit";

export const initialState = {
  error: null,
  loading: false,
  category: [],
  orderList: [],
  categoryItem: [],
};

const categoryReducer = createSlice({
  name: "category",
  initialState,
  reducers: {
    addCategory(state) {
      state.loading = true;
    },
    setAddCategory(state, { payload }) {
      state.loading = false;
      state.category = payload.data.category;
    },

    getCategory(state) {
      state.loading = true;
    },
    setCategory(state, { payload }) {
      state.loading = false;
      state.category = payload.data.data;
    },
    setDeleteCategory(state) {
      state.loading = false;
    },
    deleteCategory(state, { payload }) {
      state.category = state?.category?.filter(
        (item) => item.gpid !== payload.item
      );
      state.category =
        payload.valuesTrue === true
          ? state?.category?.filter((item) => item.pid !== payload.item)
          : state.category;
      state.category =
        payload.subItem === true
          ? state?.category?.filter((item) => item.cid !== payload.item)
          : state.category;
      state.loading = false;
    },
    getCategoryById(state) {
      state.loading = true;
    },
    setCategoryById(state, { payload }) {
      state.loading = false;
      state.categoryItem = payload.data.data;
    },
    updateCategory(state) {
      state.loading = true;
    },
    setUpdateCategory(state, { payload }) {
      state.loading = false;
      state.category = payload.data.category;
    },
    getCategoryItemData(state) {
      state.loading = true;
    },
    setCategoryItemData(state, { payload }) {
      state.loading = false;
      state.categoryItem = payload;
    },
    setOrderList(state) {
      state.loading = false;
    },
    orderList(state, { payload }) {
      state.orderList = payload;
      state.loading = false;
    },
    actionFalied(state, { payload }) {
      state.loading = false;
      state.error = payload?.response?.data?.message;
    },
  },
});

export const { name, actions } = categoryReducer;
export default categoryReducer.reducer;
