import React, { useState, useEffect } from "react";
import { Row, Col, Button } from "react-bootstrap";
import { Form, FormikProvider, useFormik } from "formik";
import { useNavigate, useParams } from "react-router-dom";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import * as Yup from "yup";

import { actions as promotion } from "../../redux/promotion/promotionAction";
import { actions as discount } from "../../redux/discount/discountAction";
import FormikController from "../../components/controls/FormikController";
import { currencyType } from "../../shared/contant";
import Card from "../../components/card";

const initialValues = {
  values: "",
  is_active: "",
  type_id: "",
  currency: "",
  calender: "",
};

const ManagePromotion = (props) => {
  const navigate = useNavigate();

  const params = useParams();

  const [currencyList, setCurrencyList] = useState([]);

  useEffect(() => {
    setCurrencyList(
      Object.keys(currencyType).map((key) => ({
        value: key,
        label: currencyType[key],
      }))
    );
  }, []);

  const discountList = props?.discount?.discount;

  const validationSchema = Yup.object({
    values: Yup.string().required().label("value"),
  });
  const [initFormData, setInitialForm] = useState(initialValues);

  useEffect(() => {
    if (params?.id && props?.mode === "Update") {
      props.actions.getPromotionById(params);
    }
    props.actions.getDiscountType();
  }, [props.actions, params, props?.mode]);

  const promotionData = props?.promotion?.promotionItem;

  useEffect(() => {
    if (params?.id && promotionData?.id) {
      const promotion = {
        values: promotionData.values,
        is_active: promotionData.status === "Active" ? true : false,
        type_id: promotionData.type_id,
      };
      setInitialForm(promotion);
    }
  }, [params?.id, promotionData]);

  const onSubmit = (values) => {
    const data = {
      values: values?.values,
      is_active: values.is_active,
      type_id: values.type_id,
      currency: values.currency,
      calender: values.calender,
    };

    if (!params?.id && props?.mode === "Add") {
      props.actions.addPromotion({
        body: data,
        callback: () => {
          resetForm();
          navigate("/promotion/list");
        },
      });
      setInitialForm({
        values: "",
        is_active: false,
        type_id: "",
        currency: "",
        calender: "",
      });
    } else {
      props.actions.updatePromotion({
        body: { data: data, params: params.id },
        callback: () => {
          setTimeout(() => {
            resetForm();
            navigate("/promotion/list");
          }, 50);
        },
      });
    }
  };

  const formik = useFormik({
    initialValues: initFormData,
    validationSchema: validationSchema,
    enableReinitialize: true,
    onSubmit: (values) => onSubmit(values),
  });

  const { handleChange, handleSubmit, values, resetForm } = formik;

  return (
    <Row>
      <Col xl="12" lg="9">
        <Card>
          <Card.Header className="d-flex justify-content-between">
            <h3 className="header-title">
              {props?.mode === "Add" ? "Add" : "Update"} Promotion
            </h3>
          </Card.Header>
          <Card.Body>
            <div className="new-user-info">
              <FormikProvider value={formik}>
                <Form onSubmit={handleSubmit}>
                  <div className="row">
                    <div className="row">
                      <div className="col-6">
                        <FormikController
                          label="Type"
                          control="select"
                          name="type_id"
                          onChange={handleChange}
                          value={values.type_id}
                          className="selectpicker form-control"
                          options={discountList?.map(({ id, type }) => ({
                            value: id,
                            label: type,
                          }))}
                        />
                      </div>

                      <div className="col-6">
                        <FormikController
                          control="input"
                          type="text"
                          name="values"
                          classname="form-control"
                          label="Values"
                          onChange={handleChange}
                        />
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-6">
                      <FormikController
                        label="Currency"
                        control="select"
                        name="currency"
                        onChange={handleChange}
                        value={values.currency}
                        className="selectpicker form-control"
                        options={currencyList}
                      />
                    </div>
                    <div className="col-6">
                      <FormikController
                        control="input"
                        type="date"
                        name="calender"
                        classname="form-control"
                        label="calender"
                        onChange={handleChange}
                      />
                    </div>
                  </div>

                  <div className="row">
                    <FormikController
                      control="checkbox"
                      name="is_active"
                      option="is_active"
                      label=" Is active ?"
                      className="me-2 form-check-input"
                      value={values.is_active}
                      onChange={handleChange}
                    />
                  </div>

                  {props?.promotion?.error ? (
                    <span
                      style={{
                        color: "Red",
                        fontSize: "smaller",
                      }}
                    >
                      {props?.promotion?.error}
                    </span>
                  ) : null}

                  <Button
                    type="submit"
                    variant="btn btn-primary"
                    disabled={props?.promotion?.loading}
                  >
                    {props.mode === "Add" ? "Add" : "Update"} Promotion
                  </Button>
                  <Button
                    className="btn btn-btn btn-primary "
                    style={{ marginLeft: "17px" }}
                    onClick={() => {
                      resetForm();
                      navigate("/promotion/list");
                    }}
                  >
                    Cancel
                  </Button>
                </Form>
              </FormikProvider>
            </div>
          </Card.Body>
        </Card>
      </Col>
    </Row>
  );
};

const mapDispatchToProps = (dispatch) => ({
  actions: {
    ...bindActionCreators({ ...promotion, ...discount }, dispatch),
  },
});

const mapStateToProps = (state) => ({
  promotion: state.promotion,
  discount: state.discount,
});

export default connect(mapStateToProps, mapDispatchToProps)(ManagePromotion);
