import React, { useState, useEffect } from "react";
import { Row, Col } from "react-bootstrap";
import { useNavigate, useParams } from "react-router-dom";
import { useFormik } from "formik";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import * as Yup from "yup";

import { actions as discount } from "../../redux/discount/discountAction";
import FormikController from "../../components/controls/FormikController";
import FormWrapper from "../../components/wrapper/form-wrapper";
import Confirmation from "../../components/dialog/confirmation";

const initialValues = {
  type_id: "",
  is_active: "",
  values: "",
  description: "",
  isfeature: "",
};

const ManageDiscount = (props) => {
  const { mode } = props;

  const navigate = useNavigate();

  const params = useParams();

  const [initFormData, setInitFormData] = useState(initialValues);
  const [discountType, setDiscountType] = useState([]);
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);

  const discountList = props?.discount?.discount;

  const validationSchema = Yup.object({
    type_id: Yup.string().required(),
    is_active: Yup.string(),
    isfeature: Yup.string(),
    description: Yup.string().required(),
    values: Yup.number().required(),
  });

  const formik = useFormik({
    initialValues: initFormData,
    validationSchema: validationSchema,
    enableReinitialize: true,
    onSubmit: (values) => onSubmit(values),
  });

  const { handleChange, handleSubmit, values, resetForm } = formik;

  useEffect(() => {
    props.actions.getDiscountType();
    if (discountList.length === 0) {
      props.actions.getDiscount();
    }
  }, [props.actions, discountList]);

  const confirmUpdate = async () => {
    const id = featuredDiscount?.[0]?.id;
    const data = {
      isfeature: false,
      is_active: featuredDiscount?.[0]?.status === "Active",
    };
    props.actions.updateDiscount({
      body: { data, params: id },
      callback: () => {
        setTimeout(() => {
          submitData();
          resetForm();
        }, 200);
      },
    });
    setShow(false);
  };

  useEffect(() => {
    if (params?.id) {
      props.actions.getDiscountById(params);
    }
  }, [params, props.actions]);

  const discountItem = props?.discount?.discountItem;
  const discountTypeList = props?.discount?.discountType;

  useEffect(() => {
    setDiscountType(
      discountTypeList.map(({ id, type }) => ({ label: type, value: id }))
    );
  }, [discountTypeList]);

  useEffect(() => {
    if (params?.id) {
      const data = {
        type_id: discountItem?.type_id,
        is_active: discountItem?.status === "Active",
        values: discountItem?.values,
        description: discountItem?.description,
        isfeature: discountItem?.feature,
      };
      setInitFormData(data);
    }
  }, [params?.id, discountItem]);

  const featuredDiscount = discountList?.filter(({ feature }) => feature);

  const onSubmit = () => {
    if (
      featuredDiscount?.length &&
      parseInt(params.id) !== featuredDiscount?.[0]?.id &&
      values.isfeature
    ) {
      return setShow(true);
    }
    submitData();
  };

  const submitData = () => {
    const data = {
      type_id: values.type_id,
      is_active: values.is_active,
      isfeature: values.isfeature,
      values: values.values,
      description: values.description,
    };

    if (!params?.id && props?.mode === "Add") {
      props.actions.addDiscount({
        body: data,
        callback: () => {
          resetForm();
          navigate("/discount/discount-list");
        },
      });
    } else {
      props.actions.updateDiscount({
        body: { data, params: params.id },
        callback: () => {
          resetForm();
          setTimeout(() => navigate("/discount/discount-list"), 200);
        },
      });
    }
  };

  // const validationSchema = Yup.object({
  //   type_id: Yup.string().required("Required"),
  //   is_active: Yup.string(),
  //   values: Yup.number().required(),
  // });

  // const formik = useFormik({
  //   initialValues: initFormData,
  //   validationSchema: validationSchema,
  //   enableReinitialize: true,
  //   onSubmit: (values) => onSubmit(values),
  // });

  // const { handleChange, handleSubmit, values } = formik;

  return (
    <Row>
      <Col xl="12" lg="9">
        <FormWrapper
          title={`${props?.mode === "Add" ? "Add" : "Update"} Discount`}
          formik={formik}
          buttonTitle={mode === "Add" ? "Add Discount  " : "Update Discount  "}
          cancelClick={() => navigate("/discount/discount-list")}
          loading={props.discount.loading}
          handleSubmit={handleSubmit}
        >
          <div className="row">
            <div className="col-6">
              <FormikController
                control="select"
                label="Discount Type"
                name="type_id"
                onChange={handleChange}
                className="selectpicker form-control"
                options={discountType}
                value={values.type_id}
              />
            </div>
            <div className="col-6">
              <FormikController
                control="input"
                type="text"
                label="Value"
                name="values"
                onChange={handleChange}
                className="form-control"
                value={values.values}
              />
            </div>
          </div>

          <div className="row">
            <FormikController
              control="textarea"
              type="text"
              name="description"
              className="form-control"
              label="Description"
              onChange={handleChange}
              value={values.description}
            />
          </div>

          <div className="row">
            <div className="col-md-6">
              <FormikController
                control="checkbox"
                name="is_active"
                option="is_active"
                label="Is Active ?"
                className="me-2 form-check-input"
                value={values?.is_active}
                onChange={handleChange}
              />
            </div>

            <div className="col-md-6">
              <FormikController
                control="checkbox"
                name="isfeature"
                option="isfeature"
                label="Is feature ?"
                className="me-2 form-check-input"
                value={values?.isfeature}
                onChange={handleChange}
              />
            </div>
          </div>

          <Confirmation
            show={show}
            handleClose={handleClose}
            description={`Discount ${featuredDiscount?.[0]?.DiscountType?.type} of value ${featuredDiscount?.[0]?.values} is set as FEATURED. Do you really want to update ?`}
            handleClick={() => confirmUpdate()}
            yesText="Yes"
            noText="No"
          />
        </FormWrapper>
      </Col>
    </Row>
  );
};

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(discount, dispatch) },
});

const mapStateToProps = (state) => ({
  discount: state.discount,
});

export default connect(mapStateToProps, mapDispatchToProps)(ManageDiscount);
