import { call, put, takeEvery } from "redux-saga/effects";
import { actions } from "./loyalitypointAction";
import { api, endPoints } from "../../utils/api";

function* getLoyalityPoint() {
  try {
    const response = yield call(api.get, endPoints.GET_SETTING);
    yield put(actions.setLoyalityPoint(response.data));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* updateLoyalityPoint({ payload }) {
  try {
    const response = yield call(
      api.put,
      endPoints.UPDATE_SETTING,
      payload.body,
      {
        urlParams: { id: payload.id },
      }
    );

    yield put(actions.setUpdateLoyalityPoint(response?.data?.data));

    if (response.data) {
      payload.callback(response?.data?.data?.id);
    }
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

export function* loyalitypointSaga() {
  yield takeEvery(actions.getLoyalityPoint, getLoyalityPoint);
  yield takeEvery(actions.updateLoyalityPoint, updateLoyalityPoint);
}
