import React, { useCallback, useEffect, useState } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { useNavigate, useParams } from "react-router-dom";
import { Row, Col, Button } from "react-bootstrap";
import * as Yup from "yup";
import { Form, FormikProvider, useFormik } from "formik";

import Card from "../../components/card";
import { actions as category } from "../../redux/category/categoryAction";
import FormikController from "../../components/controls/FormikController";
import Dropzone from "../../components/controls/fields/Dropzone";
import { API_URL } from "../../utils/api";
import { base64ImageToBlob, getBase64FromUrl } from "../../shared/core-utils";

const initialValues = {
  category_name: "",
  file: [],
  is_active: "",
  is_feature: "",
};

const ManageCategory = (props) => {
  const params = useParams();
  const navigate = useNavigate();

  const [initFormData, setInitFormData] = useState(initialValues);
  const [file, setFile] = useState([]);
  const [newList, setNewList] = useState([]);

  const onDrop = (acceptedFiles) => {
    setFile(
      acceptedFiles.map((file) => {
        return Object.assign(file, {
          preview: URL.createObjectURL(file),
        });
      })
    );
  };

  const categoryData = props?.category?.categoryItem;
  const categoryList = props?.category?.category;

  const validationSchema = Yup.object({
    category_name: Yup.string().required("Required"),
    file: Yup.array(),
    is_active: Yup.string(),
    is_feature: Yup.string(),
  });

  const formik = useFormik({
    initialValues: initFormData,
    validationSchema: validationSchema,
    enableReinitialize: true,
    onSubmit: (values) => onSubmit(values),
  });

  const { handleChange, handleSubmit, values, resetForm } = formik;

  useEffect(() => {
    props.actions.getCategoryById(params);
    props.actions.getCategory();
  }, [props.actions, params]);

  useEffect(() => {
    setNewList(
      categoryList
        ?.filter(({ cid }) => !cid || cid === 0)
        ?.map(({ pid, gpid, pname, gpname }) => {
          let id = "";
          let name = "";
          if (gpid !== 0 && pid === 0) {
            id = gpid;
            name = gpname;
          } else if (gpid !== 0 && pid !== 0) {
            id = pid;
            name = pname;
          }
          return { value: id, label: name };
        })
    );
  }, [categoryList]);

  const setImage = useCallback(async () => {
    const image = `${API_URL}/static/category/${categoryData?.image}`;
    const base64 = await getBase64FromUrl(image);
    const convertedFile = base64ImageToBlob(base64);
    setFile([convertedFile]);
  }, [categoryData?.image]);

  useEffect(() => {
    if (params?.id && categoryData?.id) {
      const category = {
        file: [categoryData?.image],
        category_name: categoryData?.name,
        is_active: categoryData?.status === "Active" ? true : false,
        is_feature: categoryData?.feature,
        parent_id: categoryData?.parent_id,
      };
      setInitFormData(category);

      setImage();
    }
  }, [params?.id, categoryData, setImage]);

  const catID = newList?.map((list) => list.value);

  const uniqueCategory = newList?.filter(
    ({ value }, index) => !catID.includes(value, index + 1)
  );

  const compare = (a, b) => {
    if (a.label.toLowerCase() < b.label.toLowerCase()) {
      return -1;
    }
  };

  uniqueCategory.sort(compare);

  const finalList = uniqueCategory?.filter(
    ({ value }) => value !== parseInt(params?.id)
  );

  const onSubmit = (data) => {
    const formData = new FormData();

    formData.append("category_name", data?.category_name);
    formData.append("file", file[0]);
    formData.append("is_active", data.is_active);
    formData.append("is_feature", data.is_feature);
    formData.append("parent_id", data.parent_id);

    if (!params?.id && props?.mode === "Add") {
      props.actions.addCategory({
        body: formData,
        callback: () => {
          resetForm();
          navigate("/category/list");
        },
      });
    } else {
      props.actions.updateCategory({
        body: {
          formData,
          params: params.id,
        },
        callback: () => {
          resetForm();
          navigate("/category/list");
        },
      });
    }
  };

  return (
    <Row>
      <Col xl="12" lg="9">
        <Card
          className="mb-0 iq-content rounded-bottom"
          style={{ marginTop: "-103px" }}
        >
          <Card.Header className="d-flex justify-content-between">
            <h3 className="header-title">
              {props?.mode === "Add" ? "Add" : "Update"} Category
            </h3>
          </Card.Header>
          <Card.Body>
            <FormikProvider value={formik}>
              <Form onSubmit={handleSubmit}>
                <div className="col-3">
                  <FormikController
                    control="select"
                    label="Choose Parent Category"
                    name="parent_id"
                    onChange={handleChange}
                    className="selectpicker form-control"
                    options={finalList}
                    value={values.parent_id}
                  />
                </div>
                <div className="row">
                  <div className="col-6">
                    <FormikController
                      control="input"
                      type="text"
                      name="category_name"
                      className="form-control"
                      label="Category Name"
                      onChange={handleChange}
                    />
                  </div>

                  <div className="col-6">
                    <Dropzone
                      label="Upload Image"
                      file={file}
                      name="file"
                      onDrop={onDrop}
                      maxFiles={1}
                      isMultiple={false}
                    />
                  </div>
                </div>

                <div className="row">
                  <div className="col-2">
                    <FormikController
                      control="checkbox"
                      name="is_active"
                      option="is_active"
                      label="Is Active ?"
                      className="me-2 form-check-input"
                      value={values?.is_active}
                      onChange={handleChange}
                    />
                  </div>

                  <div className="col-2">
                    <FormikController
                      control="checkbox"
                      name="is_feature"
                      option="is_feature"
                      label="Is Feature ?"
                      className="me-2 form-check-input"
                      value={values?.is_feature}
                      onChange={handleChange}
                    />
                  </div>
                </div>

                {props?.category?.error ? (
                  <span
                    style={{
                      color: "Red",
                      fontSize: "smaller",
                    }}
                  >
                    {props?.category?.error}
                  </span>
                ) : null}

                <div className="d-flex justify-content-start">
                  <Button
                    type="submit"
                    variant="primary"
                    disabled={props?.category?.loading}
                    className="mt-3"
                  >
                    {props?.mode === "Add" ? "Add" : "Update"} Category
                  </Button>
                  <Button
                    className="mt-3"
                    style={{ marginLeft: "17px" }}
                    onClick={() => {
                      resetForm();
                      navigate("/category/list");
                    }}
                  >
                    Cancel
                  </Button>
                </div>
              </Form>
            </FormikProvider>
          </Card.Body>
        </Card>
      </Col>
    </Row>
  );
};

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(category, dispatch) },
});

const mapStateToProps = (state) => ({
  category: state.category,
});

export default connect(mapStateToProps, mapDispatchToProps)(ManageCategory);
