import React, { useCallback, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Row, Col, Image } from "react-bootstrap";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";

import { ReactComponent as EditIcon } from "../../assets/images/icons/Pen-Icon.svg";
import { ReactComponent as DeleteIcon } from "../../assets/images/icons/Bin-Icon.svg";
import { actions as banner } from "../../redux/banner/bannerAction";
import TableWrapper from "../../components/wrapper/table-wrapper";
import Confirmation from "../../components/dialog/confirmation";
import Pagination from "../pagination/pagination";
import { API_URL } from "../../utils/api";

function BannerList(props) {
  const navigate = useNavigate();

  const [showItems, setShowItems] = useState(10);
  const handleShowItemChange = (e) => {
    setShowItems(e.target.value);
  };

  const [show, setShow] = useState(false);
  const [id, setID] = useState();

  const handleDelete = (item) => (e) => {
    setID(item.id);
    e.preventDefault();
    setShow(true);
  };

  const handleClose = () => setShow(false);

  const confirmDelete = (id) => {
    props.actions.deleteBanner(id);
    setShow(false);
  };

  const handleItemGet = (item) => (e) => {
    e.preventDefault();
    navigate(`/banner/update/${item.id}`);
  };

  const [inputText, setInputText] = useState("");
  let inputHandler = (e) => {
    var lowerCase = e.target.value.toLowerCase();
    setInputText(lowerCase);
  };

  const Data = props?.banner?.banner?.map((item) => {
    return item;
  });

  const filteredData = Data?.filter((el) => {
    if (inputText === "") {
      return el;
    } else {
      return el?.title?.toLowerCase().includes(inputText);
    }
  });

  let NUM_OF_RECORDS = filteredData?.length;

  const [currentPage, setCurrentPage] = useState(1);

  const onPageChanged = useCallback(
    (event, page) => {
      event.preventDefault();
      setCurrentPage(page);
    },
    [setCurrentPage]
  );

  const currentData = filteredData?.slice(
    (currentPage - 1) * showItems,
    (currentPage - 1) * showItems + showItems
  );

  useEffect(() => {
    props.actions.getBanner();
  }, [props.actions]);

  return (
    <Row>
      <Col sm="12">
        <TableWrapper
          title="Banner List"
          buttonTitle="Add Banner"
          buttonAction={() => navigate("/banner/add")}
          inputHandler={inputHandler}
          handleShowItemChange={handleShowItemChange}
        >
          <div className="table-responsive">
            <table
              id="user-list-table"
              className="table table-striped"
              role="grid"
              data-toggle="data-table"
            >
              {filteredData?.length === 0 ? (
                <tbody>
                  <tr
                    style={{
                      display: "flex",
                      justifyContent: "center",
                    }}
                  >
                    <td>
                      <h3>No Record Found</h3>
                    </td>
                  </tr>
                </tbody>
              ) : (
                <>
                  <thead>
                    <tr className="ligth">
                      <th className="text-center col-md-2">Image</th>
                      <th className="text-center col-md-2">Title</th>
                      <th className="text-center col-md-2">Status</th>
                      <th className="text-center col-md-2" min-width="100px">
                        Action
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    {currentData?.map((item, idx) => (
                      <tr key={idx}>
                        <td className="text-center">
                          <Image
                            className="bg-soft-primary rounded img-fluid avatar-40 me-3"
                            src={`${API_URL}/static/banner/${item.image}`}
                            alt="image"
                          />
                        </td>
                        <td className="text-center">{item.title}</td>
                        <td className="text-center">
                          {item.status === "Active" ? (
                            <span
                              className="badge"
                              style={{ backgroundColor: "green" }}
                            >
                              {item.status}
                            </span>
                          ) : (
                            <span
                              className="badge"
                              style={{ backgroundColor: "red" }}
                            >
                              {item.status}
                            </span>
                          )}
                        </td>

                        <td className="text-center">
                          <div className="flex align-items-center list-user-action">
                            <button
                              className="btn btn-sm btn-icon btn-warning"
                              data-toggle="tooltip"
                              data-placement="top"
                              title=""
                              data-original-title="Edit"
                              onClick={handleItemGet(item)}
                            >
                              <span className="btn-inner">
                                <EditIcon />
                              </span>
                            </button>
                            <button
                              className="btn btn-sm btn-icon btn-danger"
                              data-toggle="tooltip"
                              data-placement="top"
                              data-original-title="Delete"
                              onClick={handleDelete(item)}
                            >
                              <span className="btn-inner">
                                <DeleteIcon />
                              </span>
                            </button>

                            <Confirmation
                              show={show}
                              handleClose={handleClose}
                              description="Are you sure you want to delete ?"
                              handleClick={() => confirmDelete(id)}
                              yesText="Yes"
                              noText="No"
                            />
                          </div>
                        </td>
                      </tr>
                    ))}

                    {props?.banner?.error ? (
                      <span
                        style={{
                          color: "Red",
                          fontSize: "smaller",
                        }}
                      >
                        {props?.banner?.error}
                      </span>
                    ) : null}
                  </tbody>
                </>
              )}
            </table>
          </div>
        </TableWrapper>
      </Col>

      <span className="col-md-6">
        Showing {(currentPage - 1) * showItems + 1} to{" "}
        {(currentPage - 1) * showItems + showItems > NUM_OF_RECORDS
          ? NUM_OF_RECORDS
          : showItems}{" "}
        of {NUM_OF_RECORDS} entries
      </span>

      <Pagination
        totalRecords={NUM_OF_RECORDS}
        pageLimit={showItems}
        onPageChanged={onPageChanged}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
      />
    </Row>
  );
}
const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(banner, dispatch) },
});

const mapStateToProps = (state) => ({
  banner: state.banner,
});

export default connect(mapStateToProps, mapDispatchToProps)(BannerList);
