import React, { useState, useEffect, useCallback } from "react";
import { Row, Col, Image } from "react-bootstrap";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";

// import { ReactComponent as DeleteIcon } from "../../assets/images/icons/Bin-Icon.svg";
// import { ReactComponent as EditIcon } from "../../assets/images/icons/Pen-Icon.svg";
import TableWrapper from "../../components/wrapper/table-wrapper";
import { actions as user } from "../../redux/user/userAction";
import Pagination from "../pagination/pagination";
import { API_URL } from "../../utils/api";

const UserList = (props) => {
  const [currentPage, setCurrentPage] = useState(1);
  const [showItems, setShowItems] = useState(10);

  const Data = props?.user?.userData?.map((item) => {
    return item;
  });

  const [inputText, setInputText] = useState("");

  let inputHandler = (e) => {
    var lowerCase = e.target.value.toLowerCase();
    setInputText(lowerCase);
  };

  const filteredData = Data?.filter((el) => {
    if (!inputText) {
      return el;
    } else {
      return (
        el?.first_name?.toLowerCase().includes(inputText) ||
        el?.last_name?.toLowerCase().includes(inputText) ||
        el?.email?.toLowerCase().includes(inputText)
      );
    }
  });

  let NUM_OF_RECORDS = filteredData?.length;

  const onPageChanged = useCallback(
    (event, page) => {
      event.preventDefault();
      setCurrentPage(page);
    },
    [setCurrentPage]
  );

  const currentData = filteredData?.slice(
    (currentPage - 1) * showItems,
    (currentPage - 1) * showItems + showItems
  );

  const handleShowItemChange = (e) => {
    setShowItems(e.target.value);
  };

  useEffect(() => {
    props.actions.getUserList();
  }, [props.actions]);

  return (
    <Row>
      <Col sm="12">
        <TableWrapper
          title="User List"
          handleShowItemChange={handleShowItemChange}
          inputHandler={inputHandler}
        >
          <div className="table-responsive image-tbl">
            <table
              id="user-list-table"
              className="table table-striped"
              role="grid"
              data-toggle="data-table"
            >
              {currentData?.length === 0 ? (
                <tbody>
                  <tr
                    style={{
                      display: "flex",
                      justifyContent: "center",
                    }}
                  >
                    <td>
                      <h3>No Record Found</h3>
                    </td>
                  </tr>
                </tbody>
              ) : (
                <>
                  <thead>
                    <tr className="ligth">
                      <th className="text-center col-2">Profile</th>
                      <th className="text-center">Name</th>
                      <th className="text-center">Email</th>
                      <th className="text-center">Status</th>
                      {/* <th className="text-center" min-width="100px">
                        Action
                      </th> */}
                    </tr>
                  </thead>
                  <tbody>
                    {currentData?.map((item, idx) => (
                      <tr key={idx}>
                        <td className="text-center ">
                          {item.image === null ? (
                            <div className=" bg-soft-primary rounded img-fluid avatar-40 me-3">
                              <span>{item?.first_name[0].toUpperCase()}</span>
                            </div>
                          ) : (
                            <Image
                              className="bg-soft-primary rounded img-fluid avatar-40 me-3"
                              src={`${API_URL}/static/profile/${item.image}`}
                            />
                          )}
                        </td>

                        <td className="text-center">
                          {item?.first_name} {item?.last_name}
                        </td>
                        <td className="text-center">{item?.email}</td>
                        <td className="text-center">
                          {item?.status === "Active" ? (
                            <span
                              className="badge"
                              style={{ backgroundColor: "green" }}
                            >
                              {item?.status}
                            </span>
                          ) : (
                            <span
                              className="badge"
                              style={{ backgroundColor: "red" }}
                            >
                              {item?.status}
                            </span>
                          )}
                        </td>
                        {/* <td className="text-center">
                          <div className="flex align-items-center list-user-action">
                            <button
                              className="btn btn-sm btn-icon btn-warning"
                              data-toggle="tooltip"
                              data-placement="top"
                              title=""
                              data-original-title="Edit"
                              to="#"
                            >
                              <span className="btn-inner">
                                <EditIcon />
                              </span>
                            </button>
                            <button
                              className="btn btn-sm btn-icon btn-danger"
                              data-toggle="tooltip"
                              data-placement="top"
                              title=""
                              data-original-title="Delete"
                              to="#"
                            >
                              <span className="btn-inner">
                                <DeleteIcon />
                              </span>
                            </button>
                          </div>
                        </td> */}
                      </tr>
                    ))}

                    {props?.user?.error ? (
                      <span
                        style={{
                          color: "Red",
                          fontSize: "smaller",
                        }}
                      >
                        {props?.user?.error}
                      </span>
                    ) : null}
                  </tbody>
                </>
              )}
            </table>
          </div>
        </TableWrapper>
      </Col>

      <span className="col-md-6">
        Showing {(currentPage - 1) * showItems + 1} to{" "}
        {(currentPage - 1) * showItems + showItems > NUM_OF_RECORDS
          ? NUM_OF_RECORDS
          : showItems}{" "}
        of {NUM_OF_RECORDS} entries
      </span>

      <Pagination
        totalRecords={NUM_OF_RECORDS}
        pageLimit={showItems}
        onPageChanged={onPageChanged}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
      />
    </Row>
  );
};

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(user, dispatch) },
});

const mapStateToProps = (state) => ({
  user: state.user,
});

export default connect(mapStateToProps, mapDispatchToProps)(UserList);
