import { createSlice } from "@reduxjs/toolkit";

export const initialState = {
  error: null,
  loading: false,
  order: [],
  orderItem: [],
};

const orderReducer = createSlice({
  name: "order",
  initialState,
  reducers: {
    getOrder(state) {
      state.loading = true;
    },
    setOrder(state, { payload }) {
      state.loading = false;
      state.order = payload.data.data;
    },
    getOrderById(state) {
      state.loading = true;
    },
    setOrderById(state, { payload }) {
      state.loading = false;
      state.orderItem = payload.data.data;
    },
    updateOrder(state) {
      state.loading = true;
    },
    setUpdateOrder(state, { payload }) {
      state.loading = false;
      state.order = payload.data.order;
    },
    getOrderItemData(state) {
      state.loading = false;
    },
    setOrderItemData(state, { payload }) {
      state.loading = false;
      state.orderItem = payload;
    },
    actionFalied(state, { payload }) {
      state.loading = false;
      state.error = payload?.response?.data?.message;
    },
  },
});

export const { name, actions } = orderReducer;
export default orderReducer.reducer;
