import React, { useState } from "react";
import { Row, Col } from "react-bootstrap";
import { Form, FormikProvider, useFormik } from "formik";
import * as Yup from "yup";

import FormikController from "../../components/controls/FormikController";
import Card from "../../components/card";

const initialValues = {
  distance: "",
  charges: "",
};

const ManageDeliverySetting = () => {
  const [initFormData, setInitFormData] = useState(initialValues);
  const [isUpdate, setIsUpdate] = useState(false);
  const [fieldDisabled, setFieldDisabled] = useState(true);
  // const [id, setId] = useState("");

  const validationSchema = Yup.object({
    distance: Yup.number()
      .positive()
      .typeError("you must specify a number")
      .required("required"),
    charges: Yup.number()
      .positive()
      .typeError("you must specify a number")
      .required("required"),
  });

  const handleUpdate = () => {
    setFieldDisabled(false);
    setIsUpdate(!isUpdate);
  };

  const handleCancel = () => {
    setFieldDisabled(true);
    setIsUpdate(false);
    //setInitFormData(props?.loyalitypoint?.loyalitypoint);
  };

  const onSubmit = (values) => {
    setFieldDisabled(true);
    setIsUpdate(false);

    // const data = {
    //   distance: parseInt(values?.distance),
    //   charges: parseInt(values?.charges),
    // };

    setInitFormData(initialValues);
  };

  const formik = useFormik({
    initialValues: initFormData,
    validationSchema: validationSchema,
    enableReinitialize: true,
    onSubmit: (values) => onSubmit(values),
  });

  const { handleChange, handleSubmit } = formik;

  return (
    <Row>
      <Col xl="12" lg="9">
        <Card>
          <Card.Header className="d-flex justify-content-between">
            <h3 className="header-title">Delivery setting</h3>
          </Card.Header>
          <Card.Body>
            <div className="new-user-info">
              <FormikProvider value={formik}>
                <Form>
                  <div className="row">
                    <div className="col-6">
                      <FormikController
                        label="Distance "
                        control="input"
                        name="distance"
                        onChange={handleChange}
                        className="selectpicker form-control"
                        disabled={fieldDisabled}
                      />
                    </div>

                    <div className="col-6">
                      <FormikController
                        control="input"
                        type="text"
                        name="charges"
                        classname="form-control"
                        label="Charges"
                        onChange={handleChange}
                        disabled={fieldDisabled}
                      />
                    </div>
                  </div>

                  <div className="mt-4">
                    {!isUpdate ? (
                      <button
                        className="btn btn-btn btn-primary"
                        style={{
                          marginLeft: "auto",
                        }}
                        onClick={(e) => {
                          e.preventDefault();
                          handleUpdate();
                        }}
                      >
                        Update
                      </button>
                    ) : (
                      <>
                        <button
                          className="btn btn-btn btn-primary"
                          onClick={handleSubmit}
                          style={{
                            width: "100px",
                            marginLeft: "auto",
                            marginRight: "5px",
                          }}
                        >
                          Save
                        </button>
                        <button
                          className="btn btn-btn btn-primary"
                          style={{
                            width: "100px",
                            marginRight: "5px",
                          }}
                          onClick={handleCancel}
                        >
                          Cancel
                        </button>
                      </>
                    )}
                  </div>
                </Form>
              </FormikProvider>
            </div>
          </Card.Body>
        </Card>
      </Col>
    </Row>
  );
};

export default ManageDeliverySetting;
