import { createSlice } from "@reduxjs/toolkit";

export const initialState = {
  error: null,
  loading: false,
  discountType: [],
  discountTypeItem: [],
  discount: [],
  discountItem: [],
};

const discountReducer = createSlice({
  name: "discount",
  initialState,
  reducers: {
    getDiscount(state) {
      state.loading = true;
    },
    setDiscount(state, { payload }) {
      state.loading = false;
      state.discount = payload.data.data;
    },
    addDiscount(state) {
      state.loading = true;
    },
    setAddDiscount(state, { payload }) {
      state.loading = false;
      state.discount = [...(state.discount.discount || []), payload.data.data];
    },
    updateDiscount(state) {
      state.loading = true;
    },
    setUpdateDiscount(state, { payload }) {
      state.loading = false;
      state.discount = [...(state.discount.discount || []), payload];
    },
    getDiscountById(state) {
      state.loading = true;
    },
    setDiscountById(state, { payload }) {
      state.loading = false;
      state.discountItem = payload.data.data;
    },
    deleteDiscount(state) {
      state.loading = true;
    },
    setDeleteDiscount(state, { payload }) {
      state.loading = false;
      state.discount = state?.discount?.filter((item) => item.id !== payload);
    },
    getDiscountType(state) {
      state.loading = true;
    },
    setDiscountType(state, { payload }) {
      state.loading = false;
      state.discountType = payload.data.data;
    },
    getDiscountTypeById(state) {
      state.loading = true;
    },
    setDiscountTypeById(state, { payload }) {
      state.loading = false;
      state.discountTypeItem = payload.data.data;
    },
    addDiscountType(state) {
      state.loading = true;
    },
    setAddDiscountType(state, { payload }) {
      state.loading = false;
      state.discountType = [...(state.discountType || []), payload.data.data];
    },
    updateDiscountType(state) {
      state.loading = true;
    },
    setUpdateDiscountType(state, { payload }) {
      state.loading = false;
      state.discount = payload.data.discount;
    },
    getDiscountItemData(state) {
      state.loading = false;
    },
    setDishcountItemData(state, { payload }) {
      state.loading = false;
      state.discountItem = payload;
    },
    deleteDiscountType(state) {
      state.loading = false;
    },
    setDeleteDiscountType(state, { payload }) {
      state.loading = false;
      state.discountType = state?.discountType?.filter(
        (item) => item.id !== payload
      );
    },
    actionFalied(state, { payload }) {
      state.loading = false;
      state.error = payload?.response?.data?.message;
    },
  },
});

export const { name, actions } = discountReducer;
export default discountReducer.reducer;
