import axios from "axios";

// export const API_URL = "http://192.168.1.243:8080";
export const API_URL = "http://ec2-3-231-52-36.compute-1.amazonaws.com:8181"

let store;

export const injectStore = (_store) => {
  store = _store;
};

export const api = axios.create({
  baseURL: API_URL,
  headers: {
    "Content-Type": "application/json",
  },
});

api.interceptors.request.use((config) => {
  const auth = store.getState().user || null;

  if (auth) {
    config.headers["access_token"] = "Bearer " + auth.token;
  }
  if (config?.urlParams && typeof config?.url?.replace === "function") {
    Object.entries(config.urlParams || {}).forEach(([key, value]) => {
      config.url = config.url.replace(`:${key}`, value); // encodeURIComponent()
    });
  }
  return config;
});

