import { takeEvery, call, put } from "redux-saga/effects";
import { api, endPoints } from "../../utils/api";
import { actions } from "./orderAction";

function* getOrder() {
  try {
    const response = yield call(api.get, endPoints.GET_ORDER);
    yield put(actions.setOrder(response));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getOrderById({ payload }) {
  try {
    const response = yield call(api.get, endPoints.GET_ORDER_BY_ID, {
      urlParams: {
        id: payload.body,
      },
    });
    yield put(actions.setOrderById(response));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* updateOrder({ payload }) {
  try {
    const response = yield call(api.put, endPoints.UPDATE_ORDER, payload.body, {
      urlParams: { id: payload.id },
    });
    yield put(actions.setUpdateOrder(response));
    if (response.data) {
      yield call(payload.callback);
    }
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getOrderItemData({ payload }) {
  try {
    yield put(actions.setOrderItemData(payload));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

export function* orderSaga() {
  yield takeEvery(actions.getOrder, getOrder);
  yield takeEvery(actions.getOrderById, getOrderById);
  yield takeEvery(actions.updateOrder, updateOrder);
  yield takeEvery(actions.getOrderItemData, getOrderItemData);
}
