import React, { useState, useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Row, Col } from "react-bootstrap";
import * as Yup from "yup";
import { useFormik } from "formik";
import { Editor } from "react-draft-wysiwyg";
import {
  EditorState,
  convertToRaw,
  convertFromHTML,
  ContentState,
} from "draft-js";
import draftToHtml from "draftjs-to-html";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";

import "../../../node_modules/react-draft-wysiwyg/dist/react-draft-wysiwyg.css";
import { actions as newsletter } from "../../redux/newsletter/newsletterAction";
import FormikController from "../../components/controls/FormikController";
import FormWrapper from "../../components/wrapper/form-wrapper";

const initialState = {
  title: "",
  description: "",
};

const ManageNewsLetter = (props) => {
  const { mode } = props;
  const params = useParams();

  const [editorState, setEditorState] = useState(EditorState.createEmpty());

  const onEditorStateChange = (editorState) => {
    setEditorState(editorState);
  };

  const navigate = useNavigate();

  const [initFormData, setInitFormData] = useState(initialState);

  const validationSchema = Yup.object({
    title: Yup.string().required("Required"),
  });

  const _getInitialHTML = (htmlString) => {
    return convertFromHTML(htmlString);
  };

  useEffect(() => {
    if (params?.id) {
      props.actions.getNewsLetterById(params);
    }
  }, [params, props.actions]);

  const Data = props.newsletter.newsletterItem;

  useEffect(() => {
    if (params?.id) {
      const data = {
        title: Data?.title,
        description: "",
      };
      if (Data?.description) {
        const { contentBlocks, entityMap } = _getInitialHTML(Data?.description);
        setEditorState(
          EditorState.createWithContent(
            ContentState.createFromBlockArray(contentBlocks, entityMap)
          )
        );
      }
      setInitFormData(data);
    }
  }, [params?.id, Data]);

  const onSubmit = (values) => {
    const htmlContent = draftToHtml(
      convertToRaw(editorState.getCurrentContent())
    );

    const data = {
      title: values.title,
      description: htmlContent,
    };

    if (!params?.id && props?.mode === "Add") {
      props.actions.addNewsLetter({
        body: data,
        callback: () => navigate("/newsletter/list"),
      });
    } else {
      props.actions.updateNewsLetter({
        body: { data, params: params.id },
        callback: () => navigate("/newsletter/list"),
      });
    }

    setInitFormData(initialState);
  };

  const formik = useFormik({
    initialValues: initFormData,
    validationSchema: validationSchema,
    enableReinitialize: true,
    onSubmit: (values) => onSubmit(values),
  });

  const { handleChange, handleSubmit } = formik;

  return (
    <Row>
      <Col xl="12" lg="9">
        <FormWrapper
          title={`${props?.mode === "Add" ? "Add" : "Update"} News Letter`}
          formik={formik}
          buttonTitle={
            mode === "Add" ? "Add News Letter  " : "Update News Letter "
          }
          cancelClick={() => navigate("/newsletter/list")}
          handleSubmit={handleSubmit}
        >
          <div className="row">
            <div className="col-sm-12">
              <FormikController
                control="input"
                type="text"
                name="title"
                className="form-control"
                label="Title"
                onChange={handleChange}
              />
            </div>
          </div>

          <div className="row">
            <div className="col-12">
              <div
                style={{
                  border: "1px solid #E3E1E1",
                  marginTop: "10px",
                  borderRadius: "1.25rem",
                }}
              >
                <Editor
                  editorState={editorState}
                  wrapperClassName="wrapper-class"
                  editorClassName="editor-class"
                  toolbarClassName="toolbar-class"
                  onEditorStateChange={onEditorStateChange}
                />
              </div>
            </div>
          </div>
        </FormWrapper>
      </Col>
    </Row>
  );
};
const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(newsletter, dispatch) },
});

const mapStateToProps = (state) => ({
  newsletter: state.newsletter,
});

export default connect(mapStateToProps, mapDispatchToProps)(ManageNewsLetter);
