import { createSlice } from "@reduxjs/toolkit";

export const initialState = {
  error: null,
  loading: false,
  banner: [],
  bannerItem: [],
};

const bannerReducer = createSlice({
  name: "banner",
  initialState,
  reducers: {
    addBanner(state) {
      state.loading = true;
    },
    setAddBanner(state, { payload }) {
      state.loading = false;
      state.banner = payload.data.banner;
    },
    getBanner(state) {
      state.loading = true;
    },
    setBanner(state, { payload }) {
      state.loading = false;
      state.banner = payload.data.data;
    },
    setDeleteBanner(state) {
      state.loading = false;
    },
    deleteBanner(state, { payload }) {
      state.banner = state?.banner?.filter((item) => item.id !== payload);
      state.loading = false;
    },
    updateBanner(state) {
      state.loading = true;
    },
    setUpdateBanner(state, { payload }) {
      state.loading = false;
      state.banner = payload.data.banner;
    },
    getBannerById(state) {
      state.loading = true;
    },
    setBannerById(state, { payload }) {
      state.loading = false;
      state.bannerItem = payload;
    },
    actionFalied(state, { payload }) {
      state.loading = false;
      state.error = payload?.response?.data?.message;
    },
  },
});

export const { name, actions } = bannerReducer;
export default bannerReducer.reducer;
