import { takeEvery, call, put } from "redux-saga/effects";
import { api, endPoints } from "../../utils/api";
import { actions } from "./sizeAction";

function* addSize({ payload }) {
  try {
    const response = yield call(api.post, endPoints.ADD_SIZE, payload.body);
    yield put(actions.setAddSize(response));
    payload.callback();
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getSize() {
  try {
    const response = yield call(api.get, endPoints.GET_SIZE);
    yield put(actions.setSize(response));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* deleteSize({ payload }) {
  try {
    yield call(api.delete, endPoints.DELETE_SIZE, {
      urlParams: { id: payload },
    });
    yield put(actions.setDeleteSize(payload));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getSizeById({ payload }) {
  try {
    const response = yield call(api.get, endPoints.GET_SIZE_BY_ID, {
      urlParams: {
        id: payload.id,
      },
    });
    yield put(actions.setSizeById(response));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* updateSize({ payload }) {
  try {
    const response = yield call(api.put, endPoints.UPDATE_SIZE, payload.body, {
      urlParams: { id: payload.id },
    });

    yield put(actions.setUpdateSize(response));
    payload.callback();
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* addSizePrice({ payload }) {
  try {
    const response = yield call(api.post, endPoints.ADD_SIZE_PRICE, payload.body);
    yield put(actions.setAddSizePrice(response));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

export function* sizeSaga() {
  yield takeEvery(actions.addSize, addSize);
  yield takeEvery(actions.getSize, getSize);
  yield takeEvery(actions.deleteSize, deleteSize);
  yield takeEvery(actions.getSizeById, getSizeById);
  yield takeEvery(actions.updateSize, updateSize);
  yield takeEvery(actions.addSizePrice, addSizePrice);
}
