import { createSlice } from "@reduxjs/toolkit";

export const initialState = {
  error: null,
  loading: false,
  loyalitypoint: [],
};

const loyalitypointReducer = createSlice({
  name: "loyalitypoint",
  initialState,
  reducers: {
    getLoyalityPoint(state) {
      state.loading = true;
    },
    setLoyalityPoint(state, { payload }) {
      state.loading = false;
      state.loyalitypoint = payload.data;
    },
    updateLoyalityPoint(state) {
      state.loading = false;
    },
    setUpdateLoyalityPoint(state, { payload }) {
      state.loyalitypoint = payload;
      state.loading = false;
    },
  },
});

export const { name, actions } = loyalitypointReducer;
export default loyalitypointReducer.reducer;
