import { takeEvery, call, put } from "redux-saga/effects";
import { api, endPoints } from "../../utils/api";
import { actions } from "./itemAction";

function* addItem({ payload }) {
  try {
    const response = yield call(api.post, endPoints.ADD_ITEM, payload.body);
    yield put(actions.setItem(response));
    if (response?.data?.data?.id) {
      yield call(payload.callback(response?.data?.data?.id));
    }
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* addRelaventProducts({ payload }) {
  try {
    const response = yield call(
      api.post,
      endPoints.ADD_RELAVENT_PRODUCT,
      payload.body
    );
    yield put(actions.setRelaventProduct(response));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* updateItem({ payload }) {
  try {
    const response = yield call(api.put, endPoints.UPDATE_ITEM, payload.body, {
      urlParams: { id: payload.id },
    });
    yield put(actions.setUpdateItem(response));
    yield call(payload.callback());
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getRelavantProducts({ payload }) {
  try {
    const response = yield call(api.get, endPoints.GET_RELEVANT_PRODUCTS, {
      params: {
        id: payload.id,
      },
    });
    yield put(actions.setRelevantProducts(response));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getItemByID({ payload }) {
  try {
    const response = yield call(api.get, endPoints.GET_ITEM_BY_ID, {
      urlParams: { id: payload.id },
    });

    if (response) {
      yield put(actions.setItemByID(response.data.data));
    }
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getItemByCatID({ payload }) {
  try {
    const response = yield call(api.get, endPoints.GET_ITEM_BY_CAT, {
      params: { cat_id: payload.id },
    });

    if (response) {
      yield put(actions.setItemByCatID(response.data.data));
    }
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* deleteItem({ payload }) {
  try {
    const response = yield call(api.delete, endPoints.DELETE_ITEM, {
      urlParams: {
        id: payload.id,
      },
    });

    if (response) {
      yield put(actions.setDeleteItem(payload.id));
    }
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

export function* itemSaga() {
  yield takeEvery(actions.addItem, addItem);
  yield takeEvery(actions.addRelaventProducts, addRelaventProducts);
  yield takeEvery(actions.getItemByID, getItemByID);
  yield takeEvery(actions.getItemByCatID, getItemByCatID);
  yield takeEvery(actions.getRelavantProducts, getRelavantProducts);
  yield takeEvery(actions.updateItem, updateItem);
  yield takeEvery(actions.deleteItem, deleteItem);
}
