import React, { useState, useEffect } from "react";
import { Row, Col, Button } from "react-bootstrap";
import { useParams, useNavigate } from "react-router-dom";
import * as Yup from "yup";
import { Form, FormikProvider, useFormik } from "formik";
import Card from "../../components/card";
import FormikController from "../../components/controls/FormikController";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { actions as size } from "../../redux/size/sizeAction";

const initialState = {
  size: "",
  is_active: false,
};

function ManageSize(props) {
  const navigate = useNavigate();
  const params = useParams();

  const validationSchema = Yup.object({
    size: Yup.string().required("Required"),
  });

  useEffect(() => {
    if (params.id) {
      props.actions.getSizeById({ id: params.id });
    }
  }, [params.id, props.actions]);

  const [initFormData, setInitFormData] = useState(initialState);

  const formik = useFormik({
    initialValues: initFormData,
    validationSchema: validationSchema,
    enableReinitialize: true,
    onSubmit: (values) => onSubmit(values),
  });

  const { handleChange, handleSubmit, values, resetForm } = formik;

  const size = props?.size?.sizeItem;

  useEffect(() => {
    if (params?.id && size?.id) {
      const sizeData = {
        size: size?.size,
        is_active: size?.status === "Active" ? true : false,
      };
      setInitFormData(sizeData);
    }
  }, [params?.id, size]);

  const onSubmit = (data) => {
    if (props?.mode === "Add") {
      props.actions.addSize({
        body: data,
        callback: () => {
          resetForm();
          navigate("/size/list");
        },
      });
    } else {
      props.actions.updateSize({
        body: data,
        id: params.id,
        callback: () => {
          resetForm();
          navigate("/size/list");
        },
      });
    }
  };

  return (
    <>
      <Row>
        <Col xl="10" lg="9">
          <Card
            className="mb-0 iq-content rounded-bottom"
            style={{ marginTop: "-103px" }}
          >
            <Card.Header className="d-flex justify-content-between">
              <h3 className="header-title">
                {props.mode === "Add" ? "Add" : "Update"} Size
              </h3>
            </Card.Header>

            <Card.Body>
              <FormikProvider value={formik}>
                <Form onSubmit={handleSubmit}>
                  <div className="row">
                    <div className="col-sm-12">
                      <FormikController
                        control="input"
                        type="text"
                        name="size"
                        className="form-control"
                        label="Size"
                        onChange={handleChange}
                      />
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-md-6">
                      <FormikController
                        control="checkbox"
                        name="is_active"
                        option="is_active"
                        label="Is Active ?"
                        className="me-2 form-check-input"
                        value={values?.is_active}
                        onChange={handleChange}
                      />
                    </div>
                  </div>
                  {props?.auth?.error ? (
                    <span
                      style={{
                        color: "Red",
                        fontSize: "smaller",
                      }}
                    >
                      {props?.auth?.error}
                    </span>
                  ) : null}
                  <div className="d-flex justify-content-start">
                    <Button
                      type="submit"
                      variant="btn btn-primary"
                      disabled={props?.size?.loading}
                      className="mt-3"
                    >
                      {props.mode === "Add" ? "Add" : "Update"} Size
                    </Button>
                    <Button
                      className="btn btn-btn btn-primary mt-3"
                      style={{ marginLeft: "17px" }}
                      onClick={() => {
                        resetForm();
                        navigate("/size/list");
                      }}
                    >
                      Cancel
                    </Button>
                  </div>
                </Form>
              </FormikProvider>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </>
  );
}
const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(size, dispatch) },
});

const mapStateToProps = (state) => ({
  size: state.size,
});

export default connect(mapStateToProps, mapDispatchToProps)(ManageSize);
