import React, { useEffect, useState } from "react";
import { Row, Col, Form, Button, Image } from "react-bootstrap";
import { Formik, useFormik } from "formik";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { API_URL } from "../../utils/api";
import { useParams, useNavigate } from "react-router-dom";
import * as Yup from "yup";

import FormikController from "../../components/controls/FormikController";
import { ORDER_STATUS, PAYMENT_STATUS } from "../../shared/contant";
import { actions as order } from "../../redux/order/orderAction";
import "../../assets/scss/custom/Table.scss";
import Card from "../../components/card";

const initialValues = {
  order_status: "",
  payment_status: "",
};
const OrderD = (props) => {
  const params = useParams();
  const navigate = useNavigate();

  const [paymentStatusList, setPaymentStatusList] = useState();
  const [orderStatusList, setOrderStatusList] = useState();
  const [initFormData, setInitialForm] = useState(initialValues);

  const onSubmit = (value) => {
    const data = {
      payment_status: value.payment_status,
      order_status: value.order_status,
    };

    props.actions.updateOrder({
      body: data,
      id: params.id,
      callback: () =>
        setTimeout(() => {
          navigate("/order/list");
        }, 50),
    });
  };

  const data = props?.order?.orderItem[0];

  useEffect(() => {
    if (params.id) {
      const values = {
        order_status: data?.order_status,
        payment_status: data?.payment_status,
      };
      setInitialForm(values);
    }
  }, [data, params.id]);

  useEffect(() => {
    props.actions.getOrderById({ body: params.id });
  }, [params?.id, props?.actions]);

  useEffect(() => {
    setPaymentStatusList(
      Object.keys(PAYMENT_STATUS).map((key) => ({
        value: PAYMENT_STATUS[key],
        label: key,
      }))
    );
    setOrderStatusList(
      Object.keys(ORDER_STATUS).map((key) => ({
        value: ORDER_STATUS[key],
        label: key,
      }))
    );
  }, []);

  const validationSchema = Yup.object({
    order_status: Yup.string().required(),
    payment_status: Yup.string().required(),
  });

  const formik = useFormik({
    initialValues: initFormData,
    validationSchema: validationSchema,
    enableReinitialize: true,
    onSubmit: (values) => onSubmit(values),
  });

  const { handleChange, handleSubmit, values } = formik;

  const orderDetails = props?.order;

  const Data = props?.order?.orderItem[0]?.OrderDetails;

  return (
    <Row style={{ display: "flex", justifyContent: "space-around" }}>
      <Col sm="6">
        {props?.order?.error ? (
          <span
            style={{
              color: "Red",
              fontSize: "smaller",
            }}
          >
            {props?.order?.error}
          </span>
        ) : null}

        <Card>
          <Card.Header>
            <Card.Header.Title>
              <h4 className="card-title"> Order Details</h4>
            </Card.Header.Title>
          </Card.Header>

          <Card.Body className="px-0">
            <table style={{ width: "100px" }}>
              <tbody>
                <tr>
                  <th>Total Amount:</th>
                  <td>{orderDetails?.orderItem[0]?.total_amount}</td>
                </tr>
                <tr>
                  <th>Total Paid Amount:</th>
                  <td>{orderDetails?.orderItem[0]?.total_paid_amount}</td>
                </tr>
                <tr>
                  <th>Total Save Amount:</th>
                  <td>{orderDetails?.orderItem[0]?.total_save_amount}</td>
                </tr>
                <tr>
                  <th> Payment Status:</th>
                  <td>{orderDetails?.orderItem[0]?.payment_status}</td>
                </tr>
                <tr>
                  <th> Order Status:</th>
                  <td>{orderDetails?.orderItem[0]?.order_status}</td>
                </tr>
                <tr>
                  <th> Order Date:</th>
                  <td>
                    {new Date(
                      orderDetails?.orderItem[0]?.order_date
                    ).toLocaleDateString()}
                  </td>
                </tr>
              </tbody>
            </table>
          </Card.Body>
        </Card>
      </Col>

      <Col sm="6">
        <Card>
          <Card.Header>
            <Card.Header.Title>
              <h4 className="card-title"> User Details</h4>
            </Card.Header.Title>
          </Card.Header>

          <Card.Body className="px-0">
            <table style={{ width: "100px" }}>
              <tbody>
                <tr>
                  <th> Name :</th>
                  <td>
                    {`${orderDetails?.orderItem[0]?.User?.first_name} ${orderDetails?.orderItem[0]?.User?.last_name}`}
                  </td>
                </tr>
                <tr>
                  <th> email:</th>
                  <td>{orderDetails?.orderItem[0]?.User?.email}</td>
                </tr>

                <tr>
                  <th> Gender:</th>
                  <td>{orderDetails?.orderItem[0]?.User?.gender}</td>
                </tr>
                <tr>
                  <th> BirthDate:</th>
                  <td>
                    {new Date(
                      orderDetails?.orderItem[0]?.User?.birthdate
                    ).toLocaleDateString()}
                  </td>
                </tr>
              </tbody>
            </table>
          </Card.Body>
        </Card>
      </Col>

      <Col sm="12">
        <Card>
          <Card.Header className="d-flex justify-content-between">
            <h3 className="header-title">Items List</h3>
          </Card.Header>

          <div className="table-responsive">
            <table
              id="user-list-table"
              className="table table-striped"
              role="grid"
              data-toggle="data-table"
            >
              <>
                <thead>
                  <tr className="ligth" style={{ textAlign: "center" }}>
                    <th>Image</th>
                    <th>Name</th>
                    <th>Price</th>
                    <th>Quantity</th>
                  </tr>
                </thead>
                <tbody>
                  {Data?.map((item, idx) => (
                    <tr key={idx} style={{ textAlign: "center" }}>
                      <td className="text-center">
                        <Image
                          className="bg-soft-primary rounded img-fluid avatar-40 me-3"
                          src={`${API_URL}/static/items/${
                            JSON.parse(item?.Item?.images)?.[0]?.filename
                          }`}
                          alt="profile"
                        />
                      </td>
                      <td>{item?.Item?.name}</td>
                      <td>{item?.Item?.price}</td>
                      <td>{item?.quantity}</td>
                    </tr>
                  ))}

                  {props?.order?.error ? (
                    <span
                      style={{
                        color: "Red",
                        fontSize: "smaller",
                      }}
                    >
                      {props?.order?.error}
                    </span>
                  ) : null}
                </tbody>
              </>
            </table>
          </div>
        </Card>
      </Col>

      <Col xl="12" lg="9">
        <Card>
          <Card.Header className="d-flex justify-content-between">
            <h3 className="header-title">Update Order</h3>
          </Card.Header>
          <Card.Body>
            <Formik enableReinitialize={true}>
              <div className="new-user-info">
                <Form onSubmit={formik}>
                  <div className="row">
                    <div className="row mb-3 col-6">
                      <FormikController
                        label="Payment Status"
                        control="select"
                        name="payment_status"
                        onChange={handleChange}
                        value={values.payment_status}
                        className="selectpicker form-control"
                        options={paymentStatusList}
                      />
                    </div>

                    <div className="row ms-3 mb-3 col-6">
                      <FormikController
                        label="Order Status"
                        control="select"
                        name="order_status"
                        onChange={handleChange}
                        value={values.order_status}
                        className="selectpicker form-control"
                        options={orderStatusList}
                      />
                    </div>
                  </div>

                  <Button
                    type="button"
                    variant="btn btn-primary"
                    onClick={handleSubmit}
                    disabled={props?.order?.loading}
                  >
                    Update Order
                  </Button>
                  <Button
                    className="btn btn-btn btn-primary "
                    style={{ marginLeft: "17px" }}
                    onClick={() => navigate("/order/list")}
                  >
                    Cancel
                  </Button>
                </Form>
              </div>
            </Formik>
          </Card.Body>
        </Card>
      </Col>
    </Row>
  );
};

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(order, dispatch) },
});

const mapStateToProps = (state) => ({
  order: state.order,
});

export default connect(mapStateToProps, mapDispatchToProps)(OrderD);
