import { takeEvery, call, put } from "redux-saga/effects";
import { api, endPoints } from "../../utils/api";
import { actions } from "./promotionAction";

function* addPromotion({ payload }) {
  try {
    const response = yield call(api.post, endPoints.ADD_DISCOUNT, payload.body);

    yield put(actions.setAddPromotion(response));
    yield call(payload.callback);
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getPromotion({ payload }) {
  try {
    const response = yield call(api.get, endPoints.GET_DISCOUNT, payload);

    yield put(actions.setPromotion(response));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getPromotionById({ payload }) {
  try {
    const response = yield call(api.get, endPoints.GET_DISCOUNT_BY_ID, {
      urlParams: {
        id: payload.id,
      },
    });

    yield put(actions.setPromotionById(response));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* updatePromotion({ payload }) {
  try {
    const update = yield call(
      api.put,
      endPoints.UPDATE_DISCOUNT,
      payload.body.data,
      {
        urlParams: { id: payload.body.params },
      }
    );
    if (update.data) {
      yield put(actions.setUpdatePromotion(payload.body));
      payload.callback();
    }
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getPromotionItemData({ payload }) {
  try {
    yield put(actions.setPromotionItemData(payload));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* deletePromotion({ payload }) {
  try {
    yield call(api.delete, endPoints.DELETE_DISCOUNT, {
      urlParams: { id: payload },
    });

    yield put(actions.setDeletePromotion(payload));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

export function* promotionSaga() {
  yield takeEvery(actions.addPromotion, addPromotion);
  yield takeEvery(actions.getPromotion, getPromotion);
  yield takeEvery(actions.deletePromotion, deletePromotion);
  yield takeEvery(actions.updatePromotion, updatePromotion);
  yield takeEvery(actions.getPromotionById, getPromotionById);
  yield takeEvery(actions.getPromotionItemData, getPromotionItemData);
}
