import { createSlice } from "@reduxjs/toolkit";

export const initialState = {
  error: null,
  loading: false,
  item: null,
  relevantProducts: [],
  itemList: [],
};

const itemReducer = createSlice({
  name: "item",
  initialState,
  reducers: {
    addItem(state) {
      state.loading = true;
    },
    setItem(state, { payload }) {
      state.loading = false;
      state.item = payload.data;
    },
    addRelaventProducts(state) {
      state.loading = true;
    },
    setRelaventProduct(state) {
      state.loading = false;
    },
    updateItem(state) {
      state.loading = false;
    },
    setUpdateItem(state, { payload }) {
      state.loading = true;
      state.item = payload.data;
    },
    actionFalied(state, { payload }) {
      state.loading = false;
      state.error = payload?.response?.data?.message;
    },
    getRelavantProducts(state) {
      state.loading = true;
    },
    setRelevantProducts(state, { payload }) {
      state.loading = false;
      state.relevantProducts = payload?.data?.data;
    },
    getItemByCatID(state) {
      state.loading = true;
    },
    setItemByCatID(state, { payload }) {
      state.loading = false;
      state.itemList = payload;
    },
    getItemByID(state) {
      state.loading = true;
    },
    setItemByID(state, { payload }) {
      state.loading = false;
      state.item = payload;
    },
    deleteItem(state) {
      state.loading = true;
    },
    setDeleteItem(state, { payload }) {
      state.loading = false;
      state.itemList = state.itemList.filter((item) => item.id !== payload);
    },
  },
});

export const { name, actions } = itemReducer;
export default itemReducer.reducer;
