import React, { useState, useEffect, useCallback } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Row, Col } from "react-bootstrap";
import * as Yup from "yup";
import { useFormik } from "formik";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";

import { base64ImageToBlob, getBase64FromUrl } from "../../shared/core-utils";
import FormikController from "../../components/controls/FormikController";
import { actions as banner } from "../../redux/banner/bannerAction";
import Dropzone from "../../components/controls/fields/Dropzone";
import FormWrapper from "../../components/wrapper/form-wrapper";
import { API_URL } from "../../utils/api";

const initialState = {
  title: "",
  terms: "",
  is_active: false,
  file: [],
};

function ManageBanner(props) {
  const { mode } = props;
  const params = useParams();

  const navigate = useNavigate();

  const [initFormData, setInitFormData] = useState(initialState);

  const validationSchema = Yup.object({
    title: Yup.string().required("Required"),
    terms: Yup.string(),
  });

  const [images, setImages] = useState([]);

  const onDrop = (acceptedFiles) => {
    setImages(
      acceptedFiles.map((file) => {
        return Object.assign(file, {
          preview: URL.createObjectURL(file),
        });
      })
    );
  };

  useEffect(() => {
    if (params.id && props.mode === "Update")
      props.actions.getBannerById({ id: params.id });
  }, [props.actions, params, props.mode]);

  const Data = props?.banner?.bannerItem;

  const setImage = useCallback(async () => {
    const image = `${API_URL}/static/banner/${Data?.image}`;
    const base64 = await getBase64FromUrl(image);
    const convertedFile = base64ImageToBlob(base64);
    setImages([convertedFile]);
  }, [Data?.image]);

  useEffect(() => {
    if (params?.id && Data?.id) {
      const productInfo = {
        title: Data?.title,
        file: Data?.image,
        is_active: Data?.status === "Active" ? true : false,
        terms: Data?.terms,
      };

      setInitFormData(productInfo);

      setImage();
    }
  }, [params?.id, Data, setImage]);

  const onSubmit = (data) => {
    var formData = new FormData();
    formData.append("title", data?.title);
    formData.append("file", images[0]);
    formData.append("is_active", data.is_active);
    formData.append("terms", data?.terms);

    if (!params?.id && props?.mode === "Add") {
      props.actions.addBanner({
        body: formData,
        callback: () => {
          resetForm();
          navigate("/banner/list");
        },
      });
    } else {
      props.actions.updateBanner({
        body: { formData, params: params.id },
        callback: () => {
          resetForm();
          navigate("/banner/list");
        },
      });
    }
  };

  const formik = useFormik({
    initialValues: initFormData,
    validationSchema: validationSchema,
    enableReinitialize: true,
    onSubmit: (values) => onSubmit(values),
  });

  const { handleChange, handleSubmit, values, resetForm } = formik;

  return (
    <Row>
      <Col xl="12" lg="9">
        <FormWrapper
          title={`${props?.mode === "Add" ? "Add" : "Update"} Banner`}
          formik={formik}
          buttonTitle={mode === "Add" ? "Add Banner  " : "Update Banner  "}
          cancelClick={() => navigate("/banner/list")}
          loading={props.banner.loading}
          handleSubmit={handleSubmit}
        >
          <div className="row">
            <div className="col-6">
              <FormikController
                control="input"
                type="text"
                name="title"
                className="form-control"
                label="Title"
                onChange={handleChange}
              />
            </div>
            <div className="col-6">
              <FormikController
                control="textarea"
                type="text"
                name="terms"
                className="form-control"
                label="Terms & conditions"
                onChange={handleChange}
              />
            </div>
          </div>

          <div className="row">
            <div className="col-6">
              <Dropzone
                label="Upload Image"
                file={images}
                name="file"
                onDrop={onDrop}
                maxFiles={1}
                isMultiple={false}
              />
            </div>
          </div>

          <div className="row">
            <div className="col-md-6">
              <FormikController
                control="checkbox"
                name="is_active"
                option="is_active"
                label="Is Active ?"
                className="me-2 form-check-input"
                value={values?.is_active}
                onChange={handleChange}
              />
            </div>
          </div>

          {props?.banner?.error ? (
            <span
              style={{
                color: "Red",
                fontSize: "smaller",
              }}
            >
              {props?.banner?.error}
            </span>
          ) : null}
        </FormWrapper>
      </Col>
    </Row>
  );
}

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(banner, dispatch) },
});

const mapStateToProps = (state) => ({
  banner: state.banner,
});

export default connect(mapStateToProps, mapDispatchToProps)(ManageBanner);
