import React, { useEffect, useState, useCallback } from "react";
import { useNavigate } from "react-router-dom";
import { Row, Col } from "react-bootstrap";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";

import { ReactComponent as EditIcon } from "../../assets/images/icons/Pen-Icon.svg";
import { ReactComponent as DeleteIcon } from "../../assets/images/icons/Bin-Icon.svg";
import { actions as discount } from "../../redux/discount/discountAction";
import Confirmation from "../../components/dialog/confirmation";
import TableWrapper from "../../components/wrapper/table-wrapper";
import Pagination from "../pagination/pagination";
import { discountList } from "../../shared/contant";

const DiscountTypeList = (props) => {
  const navigate = useNavigate();
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const [id, setID] = useState();

  const handleDelete = (item) => {
    setID(item.id);
    setShow(true);
  };

  const confirmDelete = (id) => {
    props.actions.deleteDiscountType(id);
    setShow(false);
  };

  const handleItemGet = (item) => {
    navigate(`/discount/discount-type/update/${item.id}`);
    props.actions.getDiscountItemData(item);
  };

  useEffect(() => {
    props.actions.getDiscountType();
  }, [props.actions]);

  const [currentPage, setCurrentPage] = useState(1);
  const [showItems] = useState(10);
  const [inputText, setInputText] = useState("");

  let userlist = props?.discount?.discountType?.map((item) => {
    return item;
  });

  let inputHandler = (e) => {
    var lowerCase = e.target.value.toLowerCase();
    setInputText(lowerCase);
  };

  const filteredData = userlist?.filter((el) => {
    if (inputText === "") {
      return el;
    } else {
      return el?.type?.toLowerCase().includes(inputText);
    }
  });

  const currentData = filteredData?.slice(
    (currentPage - 1) * showItems,
    (currentPage - 1) * showItems + showItems
  );

  let NUM_OF_RECORDS = filteredData?.length;

  const onPageChanged = useCallback(
    (event, page) => {
      event.preventDefault();
      setCurrentPage(page);
    },
    [setCurrentPage]
  );

  return (
    <Row>
      <Col sm="12">
        <TableWrapper
          title="Discount Type List"
          buttonTitle="Add Discount Type"
          buttonAction={(e) => navigate("/discount/discount-type/add")}
          inputHandler={inputHandler}
        >
          <div className="table-responsive">
            <table
              id="user-list-table"
              className="table table-striped"
              role="grid"
              data-toggle="data-table"
            >
              {filteredData?.length === 0 ? (
                <tbody>
                  <tr
                    style={{
                      display: "flex",
                      justifyContent: "center",
                    }}
                  >
                    <td>
                      <h3>No Record Found</h3>
                    </td>
                  </tr>
                </tbody>
              ) : (
                <>
                  <thead>
                    <tr className="ligth">
                      <th className="text-center">Type</th>
                      <th className="text-center">Status</th>
                      <th className="text-center" min-width="100px">
                        Action
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    {currentData?.map((item, idx) => (
                      <tr key={idx}>
                        <td className="text-center">{discountList[item.type]}</td>
                        <td className="text-center">
                          {item.status === "Active" ? (
                            <span
                              className="badge"
                              style={{
                                backgroundColor: "green",
                              }}
                            >
                              {item.status}
                            </span>
                          ) : (
                            <span
                              className="badge"
                              style={{
                                backgroundColor: "red",
                              }}
                            >
                              {item.status}
                            </span>
                          )}
                        </td>
                        <td className="text-center">
                          <div className="flex align-items-center list-user-action">
                            <button
                              className="btn btn-sm btn-icon btn-warning"
                              data-toggle="tooltip"
                              data-placement="top"
                              title=""
                              data-original-title="Edit"
                              onClick={() => handleItemGet(item)}
                            >
                              <span className="btn-inner">
                                <EditIcon />
                              </span>
                            </button>

                            <button
                              className="btn btn-sm btn-icon btn-danger"
                              data-toggle="tooltip"
                              data-placement="top"
                              data-original-title="Delete"
                              onClick={() => handleDelete(item)}
                            >
                              <span className="btn-inner">
                                <DeleteIcon />
                              </span>
                            </button>

                            <Confirmation
                              show={show}
                              handleClose={handleClose}
                              description="Are you sure you want to delete ?"
                              handleClick={() => confirmDelete(id)}
                              yesText="Yes"
                              noText="No"
                            />
                          </div>
                        </td>
                      </tr>
                    ))}

                    {props?.discount?.error ? (
                      <span
                        style={{
                          color: "Red",
                          fontSize: "smaller",
                        }}
                      >
                        {props?.discount?.error}
                      </span>
                    ) : null}
                  </tbody>
                </>
              )}
            </table>
          </div>
        </TableWrapper>
      </Col>

      <span className="col-md-6">
        Showing {(currentPage - 1) * showItems + 1} to{" "}
        {(currentPage - 1) * showItems + showItems > NUM_OF_RECORDS
          ? NUM_OF_RECORDS
          : showItems}{" "}
        of {NUM_OF_RECORDS} entries
      </span>

      <Pagination
        totalRecords={NUM_OF_RECORDS}
        pageLimit={showItems}
        onPageChanged={onPageChanged}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
      />
    </Row>
  );
}

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(discount, dispatch) },
});

const mapStateToProps = (state) => ({
  discount: state.discount,
});

export default connect(mapStateToProps, mapDispatchToProps)(DiscountTypeList);
