import { createSlice } from "@reduxjs/toolkit";

export const initialState = {
  error: null,
  loading: false,
  role: null,
  roleList: [],
};

const roleReducer = createSlice({
  name: "role",
  initialState,
  reducers: {
    getRoles(state) {
      state.loading = true;
    },
    setRoleList(state, { payload }) {
      state.loading = false;
      state.roleList = payload;
    },
    getRoleById(state) {
      state.loading = true;
    },
    setRole(state, { payload }) {
      state.loading = false;
      state.role = payload;
    },
    addRole(state) {
      state.loading = true;
    },
    setAddRole(state, { payload }) {
      state.loading = false;
      state.role = payload;
    },
    deleteRole(state) {
      state.loading = true;
    },
    setDeleteRole(state, { payload }) {
      state.loading = false;
      state.roleList = state?.roleList?.filter((role) => role.id !== payload);
    },
    updateRole(state) {
      state.loading = true;
    },
    actionFailed(state, { payload }) {
      state.loading = false;
      state.error = payload;
    },
  },
});

export const { name, actions } = roleReducer;
export default roleReducer.reducer;
