import React, { useState, useEffect } from "react";
import { Row, Col } from "react-bootstrap";
import { useNavigate, useParams } from "react-router-dom";
import { useFormik } from "formik";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import * as Yup from "yup";

import { actions as discount } from "../../redux/discount/discountAction";
import FormikController from "../../components/controls/FormikController";
import { discountList } from "../../shared/contant";
import FormWrapper from "../../components/wrapper/form-wrapper";

const ManageDiscountType = (props) => {
  const { mode } = props;
  const initialValues = {
    type: "",
    is_active: false,
  };

  const navigate = useNavigate();

  const params = useParams();

  const [initFormData, setInitFormData] = useState(initialValues);
  const [discount] = useState(
    Object.keys(discountList).map((key) => ({
      label: discountList[key],
      value: key,
    }))
  );

  useEffect(() => {
    if (params?.id) {
      props.actions.getDiscountTypeById(params);
    }
  }, [params, props.actions]);

  const Data = props?.discount?.discountTypeItem;

  useEffect(() => {
    if (params?.id) {
      const data = {
        type: Data?.type,
        is_active: Data?.status === "Active" ? true : false,
      };
      setInitFormData(data);
    }
  }, [params?.id, Data]);

  const onSubmit = (values) => {
    const data = {
      type: values.type,
      is_active: values.is_active,
    };

    if (!params?.id && props?.mode === "Add") {
      props.actions.addDiscountType({
        body: data,
        callback: () => {
          resetForm();
          navigate("/discount/discount-type-list");
        },
      });
    } else {
      props.actions.updateDiscountType({
        body: { data, params: params.id },
        callback: () => {
          resetForm();
          navigate("/discount/discount-type-list");
        },
      });
    }
  };

  const validationSchema = Yup.object({
    type: Yup.string().required("Required"),
  });

  const formik = useFormik({
    initialValues: initFormData,
    validationSchema: validationSchema,
    enableReinitialize: true,
    onSubmit: (values) => onSubmit(values),
  });

  const { handleChange, handleSubmit, values, resetForm } = formik;

  return (
    <Row>
      <Col xl="12" lg="9">
        <FormWrapper
          title={`${props?.mode === "Add" ? "Add" : "Update"} Discount`}
          formik={formik}
          buttonTitle={
            mode === "Add" ? "Add Discount Type " : "Update Discount Type "
          }
          cancelClick={() => navigate("/discount/discount-type-list")}
          loading={props.discount.loading}
          handleSubmit={handleSubmit}
        >
          <div className="row col-6">
            <div className="col-sm-12 form-group">
              <FormikController
                control="select"
                label="Type"
                name="type"
                onChange={handleChange}
                className="selectpicker form-control"
                options={discount}
                value={values.type}
              />
            </div>
          </div>

          <div className="row">
            <div className="col-md-6">
              <FormikController
                control="checkbox"
                name="is_active"
                option="is_active"
                label="Is Active ?"
                className="me-2 form-check-input"
                value={values?.is_active}
                onChange={handleChange}
              />
            </div>
          </div>

          {props?.discount?.error ? (
            <span
              style={{
                color: "Red",
                fontSize: "smaller",
              }}
            >
              {props?.discount?.error}
            </span>
          ) : null}
        </FormWrapper>
      </Col>
    </Row>
  );
};

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(discount, dispatch) },
});

const mapStateToProps = (state) => ({
  discount: state.discount,
});

export default connect(mapStateToProps, mapDispatchToProps)(ManageDiscountType);
