import React, { useState, useCallback, useEffect } from "react";
import { Row, Col } from "react-bootstrap";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import Pagination from "../pagination/pagination";

import { ReactComponent as EyeIcon } from "../../assets/images/icons/Eye-Icon.svg";
import TableWrapper from "../../components/wrapper/table-wrapper";
import { actions as order } from "../../redux/order/orderAction";

const OrderList = (props) => {
  const params = useParams();
  const navigate = useNavigate();
  const [inputText, setInputText] = useState("");
  const [showItems, setShowItems] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);

  const handleShowItemChange = (e) => {
    setShowItems(e.target.value);
  };

  const allOrders = [...props?.order?.order];

  const sortedOrders = (allOrders || [])?.sort((a, b) => {
    return new Date(b.order_date).getTime() - new Date(a.order_date).getTime();
  });

  let NUM_OF_RECORDS = sortedOrders?.length;

  const onPageChanged = useCallback(
    (event, page) => {
      event.preventDefault();
      setCurrentPage(page);
    },
    [setCurrentPage]
  );

  const handleClick = (item) => {
    navigate(`/order/details/${item.id}`);
  };

  const inputHandler = (e) => {
    setInputText(e.target.value);
  };

  const filteredData = sortedOrders?.filter((el) => {
    const strAmount = el.total_amount.toString();
    const strPaid = el.total_paid_amount.toString();
    const firstName = el.User.first_name;
    const lastName = el.User.last_name;

    if (!inputText) {
      return el;
    } else {
      return (
        firstName?.toLowerCase().includes(inputText.toLowerCase()) ||
        strAmount?.includes(inputText) ||
        strPaid?.includes(inputText) ||
        lastName?.toLowerCase().includes(inputText.toLowerCase())
      );
    }
  });

  const currentData = filteredData?.slice(
    (currentPage - 1) * showItems,
    (currentPage - 1) * showItems + showItems
  );

  useEffect(() => {
    props.actions.getOrder(params);
  }, [props.actions, params]);

  return (
    <Row>
      <Col sm="12">
        <TableWrapper
          title="Order List"
          handleShowItemChange={handleShowItemChange}
          inputHandler={inputHandler}
        >
          <div className="table-responsive">
            <table
              id="user-list-table"
              className="table table-striped"
              role="grid"
              data-toggle="data-table"
            >
              {filteredData?.length === 0 ? (
                <tbody>
                  <tr
                    style={{
                      display: "flex",
                      justifyContent: "center",
                    }}
                  >
                    <td>
                      <h3>No Record Found</h3>
                    </td>
                  </tr>
                </tbody>
              ) : (
                <>
                  <thead>
                    <tr className="ligth" style={{ textAlign: "center" }}>
                      <th>Name</th>
                      <th>Amount</th>
                      <th>Paid Amount</th>
                      <th>Payment Status</th>
                      <th>Order Status</th>
                      <th>Order Date</th>
                      <th>Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    {currentData?.map((item, idx) => (
                      <tr key={idx} style={{ textAlign: "center" }}>
                        <td>
                          {`${item?.User?.first_name} ${item?.User?.last_name}`}
                        </td>
                        <td>{item?.total_amount}</td>
                        <td>{item?.total_paid_amount}</td>
                        <td>{item?.payment_status}</td>
                        <td>{item?.order_status}</td>
                        <td>
                          {new Date(item?.order_date).toLocaleDateString()}
                        </td>
                        <td>
                          {item?.action}
                          <div className="flex align-items-center list-user-action">
                            <button
                              className="btn btn-sm btn-icon btn-warning"
                              data-toggle="tooltip"
                              data-placement="top"
                              title=""
                              navigate="/order/order-details"
                              data-original-title="View"
                              onClick={() => handleClick(item)}
                            >
                              <span className="btn-inner">
                                <EyeIcon />
                              </span>
                            </button>
                          </div>
                        </td>
                      </tr>
                    ))}

                    {props?.order?.error ? (
                      <span
                        style={{
                          color: "Red",
                          fontSize: "smaller",
                        }}
                      >
                        {props?.order?.error}
                      </span>
                    ) : null}
                  </tbody>
                </>
              )}
            </table>
          </div>
        </TableWrapper>
      </Col>

      <span className="col-md-6">
        Showing {(currentPage - 1) * showItems + 1} to{" "}
        {(currentPage - 1) * showItems + showItems > NUM_OF_RECORDS
          ? NUM_OF_RECORDS
          : showItems}{" "}
        of {NUM_OF_RECORDS} entries
      </span>

      <Pagination
        totalRecords={NUM_OF_RECORDS}
        pageLimit={showItems}
        onPageChanged={onPageChanged}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
      />
    </Row>
  );
};

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(order, dispatch) },
});

const mapStateToProps = (state) => ({
  order: state.order,
});

export default connect(mapStateToProps, mapDispatchToProps)(OrderList);
