import React, { useCallback, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Row, Col } from "react-bootstrap";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";

import { ReactComponent as DeleteIcon } from "../../assets/images/icons/Bin-Icon.svg";
import { ReactComponent as EditIcon } from "../../assets/images/icons/Pen-Icon.svg";
import { actions as promotion } from "../../redux/promotion/promotionAction";
import TableWrapper from "../../components/wrapper/table-wrapper";
import Confirmation from "../../components/dialog/confirmation";
import Pagination from "../pagination/pagination";

function PromotionList(props) {
  const navigate = useNavigate();
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const [id, setID] = useState();
  const [showItems, setShowItems] = useState(10);

  const handleShowItemChange = (e) => {
    setShowItems(e.target.value);
  };

  const handleDelete = (item) => (e) => {
    setID(item.id);
    e.preventDefault();
    setShow(true);
  };

  const confirmDelete = (id) => {
    props.actions.deletePromotion(id);
    setShow(false);
  };

  const handleItemGet = (item) => (e) => {
    e.preventDefault();
    navigate(`/promotion/update/${item.id}`);
    props.actions.getPromotionItemData(item);
  };

  const Data = props?.promotion?.promotion?.map((item) => {
    return item;
  });

  const [inputText, setInputText] = useState("");

  let inputHandler = (e) => {
    var lowerCase = parseInt(e.target.value);
    setInputText(lowerCase);
  };

  const [currentPage, setCurrentPage] = useState(1);

  const onPageChanged = useCallback(
    (event, page) => {
      event.preventDefault();
      setCurrentPage(page);
    },
    [setCurrentPage]
  );

  const filteredData = Data?.filter((el) => {
    const newEl = el?.type_id.toString();
    const newValue = el?.values.toString();
    if (!inputText) {
      return el;
    } else {
      return newEl.includes(inputText) || newValue.includes(inputText);
    }
  });

  const currentData = filteredData?.slice(
    (currentPage - 1) * showItems,
    (currentPage - 1) * showItems + showItems
  );

  let NUM_OF_RECORDS = filteredData?.length;

  useEffect(() => {
    props.actions.getPromotion();
  }, [props.actions]);

  return (
    <Row>
      <Col sm="12">
        <TableWrapper
          title="Promotion List"
          buttonTitle="Add Promotion"
          buttonAction={(e) => navigate("/promotion/add")}
          inputHandler={inputHandler}
          handleShowItemChange={handleShowItemChange}
        >
          <div className="table-responsive">
            <table
              id="user-list-table"
              className="table table-striped"
              role="grid"
              data-toggle="data-table"
            >
              {filteredData?.length === 0 ? (
                <tbody>
                  <tr
                    style={{
                      display: "flex",
                      justifyContent: "center",
                    }}
                  >
                    <td>
                      <h3>No Record Found</h3>
                    </td>
                  </tr>
                </tbody>
              ) : (
                <>
                  <thead>
                    <tr className="ligth">
                      <th className="text-center">Typeid</th>
                      <th className="text-center">Values</th>
                      <th className="text-center">Status</th>
                      <th min-width="100px" className="text-center">
                        Action
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    {currentData?.map((item, idx) => (
                      <tr key={idx}>
                        <td className="text-center">{item.type_id}</td>
                        <td className="text-center">{item.values}</td>
                        <td className="text-center">
                          {item.status === "Active" ? (
                            <span
                              className="badge"
                              style={{
                                backgroundColor: "green",
                              }}
                            >
                              {item.status}
                            </span>
                          ) : (
                            <span
                              className="badge"
                              style={{
                                backgroundColor: "red",
                              }}
                            >
                              {item.status}
                            </span>
                          )}
                        </td>
                        <td className="text-center">
                          <div className="flex align-items-center list-user-action">
                            <button
                              className="btn btn-sm btn-icon btn-warning"
                              data-toggle="tooltip"
                              data-placement="top"
                              title=""
                              data-original-title="Edit"
                              onClick={handleItemGet(item)}
                            >
                              <span className="btn-inner">
                                <EditIcon />
                              </span>
                            </button>

                            <button
                              className="btn btn-sm btn-icon btn-danger btn-inner"
                              data-toggle="tooltip"
                              data-placement="top"
                              data-original-title="Delete"
                              onClick={handleDelete(item)}
                            >
                              <span className="btn-inner">
                                <DeleteIcon />
                              </span>
                            </button>

                            <Confirmation
                              show={show}
                              handleClose={handleClose}
                              description="Are you sure you want to delete ?"
                              handleClick={() => confirmDelete(id)}
                              yesText="Yes"
                              noText="No"
                            />
                          </div>
                        </td>
                      </tr>
                    ))}

                    {props?.promotion?.error ? (
                      <span
                        style={{
                          color: "Red",
                          fontSize: "smaller",
                        }}
                      >
                        {props?.promotion?.error}
                      </span>
                    ) : null}
                  </tbody>
                </>
              )}
            </table>
          </div>
        </TableWrapper>
      </Col>

      <span className="col-md-6">
        Showing {(currentPage - 1) * showItems + 1} to{" "}
        {(currentPage - 1) * showItems + showItems > NUM_OF_RECORDS
          ? NUM_OF_RECORDS
          : showItems}{" "}
        of {NUM_OF_RECORDS} entries
      </span>

      <Pagination
        totalRecords={NUM_OF_RECORDS}
        pageLimit={showItems}
        onPageChanged={onPageChanged}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
      />
    </Row>
  );
}

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(promotion, dispatch) },
});

const mapStateToProps = (state) => ({
  promotion: state.promotion,
});

export default connect(mapStateToProps, mapDispatchToProps)(PromotionList);
