import { createSlice } from "@reduxjs/toolkit";

export const initialState = {
  error: null,
  loading: false,
  promotion: [],
  promotionItem: [],
};

const promotionReducer = createSlice({
  name: "promotion",
  initialState,
  reducers: {
    setPromotion(state, { payload }) {
      state.loading = false;
      state.promotion = payload.data.data;
    },
    getPromotion(state) {
      state.loading = true;
    },
    getPromotionById(state) {
      state.loading = true;
    },
    setPromotionById(state, { payload }) {
      state.loading = false;
      state.promotionItem = payload.data.data;
    },
    addPromotion(state) {
      state.loading = true;
    },
    setAddPromotion(state, { payload }) {
      state.loading = false;
      state.promotion = [
        ...(state.promotion.promotion || []),
        payload.data.data,
      ];
    },
    setDeletePromotion(state) {
      state.loading = false;
    },
    deletePromotion(state, { payload }) {
      state.promotion = state?.promotion?.filter((item) => item.id !== payload);
      state.loading = false;
    },
    updatePromotion(state) {
      state.loading = true;
    },
    setUpdatePromotion(state, { payload, id }) {
      state.promotionItem = {
        ...(state.promotionItem.data || []),
        ...payload.data,
      };
      state.loading = false;
    },
    getPromotionItemData(state) {
      state.loading = true;
    },
    setPromotionItemData(state, { payload }) {
      state.loading = false;
      state.promotionItem = payload;
    },
    actionFalied(state, { payload }) {
      state.loading = false;
      state.error = payload?.response?.data?.message;
    },
  },
});

export const { name, actions } = promotionReducer;
export default promotionReducer.reducer;
