import React, { useState, useEffect } from "react";

const range = (from, to, step = 1) => {
  let i = from;
  const range = [];

  while (i <= to) {
    range.push(i);
    i += step;
  }

  return range;
};

const Pagination = (props) => {
  const {
    totalRecords,
    pageLimit,
    onPageChanged,
    currentPage,
    setCurrentPage,
  } = props;

  const [totalPages, setTotalPages] = useState(0);

  useEffect(() => {
    setTotalPages(Math.ceil(totalRecords / pageLimit));
  }, [pageLimit, totalRecords]);

  var pages = range(1, totalPages) || [];

  return (
    <ul className="pagination  col-md-6" style={{ zIndex: "9" }}>
      <li className="page-item">
        {currentPage > 1 ? (
          <button
            className="page-link"
            onClick={(e) => onPageChanged(e, currentPage - 1)}
          >
            Previous
          </button>
        ) : (
          <button className="page-link" disabled>
            Previous
          </button>
        )}
      </li>

      {pages.map((page, index) => {
        return (
          <li
            className={`page-item${
              parseInt(currentPage) === page ? " active" : ""
            }`}
            key={index}
          >
            {currentPage > pages.length ? setCurrentPage(1) : null}
            <span className="page-link" onClick={(e) => onPageChanged(e, page)}>
              {page}
            </span>
          </li>
        );
      })}

      <li className="page-item">
        {currentPage !== totalPages ? (
          <button
            className="page-link"
            onClick={(e) => onPageChanged(e, currentPage + 1)}
          >
            Next
          </button>
        ) : (
          <button className="page-link" disabled>
            Next
          </button>
        )}
      </li>
    </ul>
  );
};

export default Pagination;
