import { takeEvery, call, put } from "redux-saga/effects";
import { api, endPoints } from "../../utils/api";
import { actions } from "./newsletterAction";

function* addNewsLetter({ payload }) {
  try {
    const response = yield call(
      api.post,
      endPoints.ADD_NEWSLETTER,
      payload.body
    );
    yield put(actions.setAddNewsLetter(response));
    yield call(payload.callback);
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getNewsLetter() {
  try {
    const response = yield call(api.get, endPoints.GET_NEWSLETTER);
    yield put(actions.setNewsLetter(response));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getNewsLetterById({ payload }) {
  try {
    const response = yield call(api.get, endPoints.GET_NEWSLETTER_BY_ID, {
      urlParams: {
        id: payload.id,
      },
    });
    yield put(actions.setNewsLetterById(response));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* updateNewsLetter({ payload }) {
  try {
    const response = yield call(
      api.put,
      endPoints.UPDATE_NEWSLETTER,
      payload.body.data,
      { urlParams: { id: payload.body.params } }
    );
    yield put(actions.setUpdateNewsLetter(response));
    yield call(payload.callback);
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getNewsLetterItemData({ payload }) {
  try {
    yield put(actions.setNewsLetterItemData(payload));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* deleteNewsLetter({ payload }) {
  try {
    yield call(api.delete, endPoints.DELETE_NEWSLETTER, {
      urlParams: { id: payload },
    });
    yield put(actions.setDeleteNewsLetter(payload));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

export function* newsletterSaga() {
  yield takeEvery(actions.addNewsLetter, addNewsLetter);
  yield takeEvery(actions.getNewsLetter, getNewsLetter);
  yield takeEvery(actions.deleteNewsLetter, deleteNewsLetter);
  yield takeEvery(actions.getNewsLetterById, getNewsLetterById);
  yield takeEvery(actions.updateNewsLetter, updateNewsLetter);
  yield takeEvery(actions.getNewsLetterItemData, getNewsLetterItemData);
}
