import { call, put, takeEvery } from "redux-saga/effects";
import { api, endPoints } from "../../utils/api";
import { actions } from "./roles-action";

function* getRoles() {
  try {
    const response = yield call(api.get, endPoints.GET_ROLES);
    if (response) {
      yield put(actions.setRoleList(response.data.data));
    }
  } catch (error) {
    yield put(actions.actionFailed(error));
  }
}

function* addRole({ payload }) {
  try {
    const response = yield call(api.post, endPoints.ADD_ROLE, payload.body);

    if (response) {
      yield put(actions.setAddRole(response));
      payload.callback();
    }
  } catch (error) {
    yield put(actions.actionFailed(error));
  }
}

function* getRoleById({ payload }) {
  try {
    const response = yield call(api.get, endPoints.GET_ROLE_BY_ID, {
      urlParams: { id: payload.id },
    });

    if (response) {
      yield put(actions.setRole(response.data.data));
    }
  } catch (error) {
    yield put(actions.actionFailed(error));
  }
}

function* deleteRole({ payload }) {
  try {
    const response = yield call(api.delete, endPoints.DELETE_ROLE, {
      urlParams: {
        id: payload.id,
      },
    });

    if (response) {
      yield put(actions.setDeleteRole(payload.id));
    }
  } catch (error) {
    yield put(actions.actionFailed(error));
  }
}

function* updateRole({ payload }) {
  try {
    const response = yield call(api.put, endPoints.UPDATE_ROLE, payload.body, {
      urlParams: { id: payload.id },
    });

    if (response) {
      yield put(actions.setRole(response.data.data));
      payload.callback();
    }
  } catch (error) {
    yield put(actions.actionFailed);
  }
}

export function* roleSaga() {
  yield takeEvery(actions.getRoles, getRoles);
  yield takeEvery(actions.addRole, addRole);
  yield takeEvery(actions.deleteRole, deleteRole);
  yield takeEvery(actions.getRoleById, getRoleById);
  yield takeEvery(actions.updateRole, updateRole);
}
