import React, { useEffect, useState, useCallback } from "react";
import { useNavigate } from "react-router-dom";
import { Row, Col, Image } from "react-bootstrap";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";

import { ReactComponent as EditIcon } from "../../assets/images/icons/Pen-Icon.svg";
import { ReactComponent as DeleteIcon } from "../../assets/images/icons/Bin-Icon.svg";
import { actions as category } from "../../redux/category/categoryAction";
import TableWrapper from "../../components/wrapper/table-wrapper";
import Confirmation from "../../components/dialog/confirmation";
import Pagination from "../pagination/pagination";
import { API_URL } from "../../utils/api";
import "./style.css";

function CategoryList(props) {
  const [currentPage, setCurrentPage] = useState(1);
  const [showItems, setShowItems] = useState(10);

  const navigate = useNavigate();

  const [id, setID] = useState();
  const [subCatId, setSubCatID] = useState();
  const [subSubCatId, setSubSubCatID] = useState();

  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);

  const [showSubCategory, setShowSubCategory] = useState(false);
  const handleSubCategoryClose = () => setShowSubCategory(false);

  const [showSubSubCategory, setShowSubSubCategory] = useState(false);
  const handleSubSubCategoryClose = () => setShowSubSubCategory(false);

  const Data = props?.category?.category?.map((item) => {
    return item;
  });

  const [inputText, setInputText] = useState("");
  let inputHandler = (e) => {
    var lowerCase = e.target.value.toLowerCase();
    setInputText(lowerCase);
  };

  const filteredData = Data?.filter((el) => {
    if (!inputText) {
      return el;
    } else {
      return (
        el?.gpname?.toLowerCase().includes(inputText) ||
        el?.pname?.toLowerCase().includes(inputText) ||
        el?.cname?.toLowerCase().includes(inputText)
      );
    }
  });

  let NUM_OF_RECORDS = filteredData?.length;

  const onPageChanged = useCallback(
    (event, page) => {
      event.preventDefault();
      setCurrentPage(page);
    },
    [setCurrentPage]
  );

  const currentData = filteredData?.slice(
    (currentPage - 1) * showItems,
    (currentPage - 1) * showItems + showItems
  );

  const deleteCategory = (item) => {
    setID(item.gpid);
    setShow(true);
  };

  const deleteSubCategory = (item) => {
    setSubCatID(item.pid);
    setShowSubCategory(true);
  };

  const deleteSubSubCategory = (item) => {
    setSubSubCatID(item.cid);
    setShowSubSubCategory(true);
  };

  const confirmCategoryDelete = (e) => {
    e.preventDefault();
    props.actions.deleteCategory({ item: id });
    setShow(false);
  };

  const confirmSubCategoryDelete = (e) => {
    e.preventDefault();
    const subCategoryId = true;
    props.actions.deleteCategory({
      item: subCatId,
      valuesTrue: subCategoryId,
    });
    setShowSubCategory(false);
  };

  const confirmSubSubCategoryDelete = (e) => {
    e.preventDefault();
    const subItem = true;
    props.actions.deleteCategory({ item: subSubCatId, subItem: subItem });
    setShowSubSubCategory(false);
  };

  const getCategoryItem = (item) => {
    navigate(`/category/update/${item.gpid}`);
  };

  const getSubCategoryItem = (item) => {
    navigate(`/category/update/${item.pid}`);
  };

  const getItem = (item) => {
    navigate(`/category/update/${item.cid}`);
  };

  const handleShowItemChange = (e) => {
    setShowItems(e.target.value);
  };

  useEffect(() => {
    props.actions.getCategory();
  }, [props.actions]);

  return (
    <Row>
      <Col sm="12">
        <TableWrapper
          title="Category List"
          buttonTitle="Add Category"
          buttonAction={() => navigate("/category/add")}
          handleShowItemChange={handleShowItemChange}
          inputHandler={inputHandler}
        >
          <div className="table-responsive">
            <table
              id="user-list-table"
              className="table table-striped"
              role="grid"
              data-toggle="data-table"
            >
              {currentData?.length === 0 ? (
                <tbody>
                  <tr
                    style={{
                      display: "flex",
                      justifyContent: "center",
                    }}
                  >
                    <td>
                      <h3>No Record Found</h3>
                    </td>
                  </tr>
                </tbody>
              ) : (
                <>
                  <thead>
                    <tr className="ligth">
                      <th className="text-center">Category</th>
                      <th className="text-center">Sub-Category</th>
                      <th className="text-center">Sub-Sub-Category</th>
                    </tr>
                  </thead>

                  <tbody>
                    {currentData?.map((item, idx) => (
                      <tr key={idx}>
                        <td className="text-center">
                          <div className="maindiv">
                            {/* <div
                              className={
                                item?.gpstatus === "Active" ? "status" : ""
                              }
                            /> */}
                            <div className="col-md-2">
                              <span>
                                <Image
                                  className="bg-soft-primary rounded img-fluid avatar-40 me-3"
                                  src={`${API_URL}/static/category/${item.gpimage}`}
                                  alt="image"
                                />
                              </span>
                            </div>
                            <div className="col-md-6">
                              <span className="cat-name">{item?.gpname}</span>
                            </div>
                            <div className="col-md-3">
                              <div className="flex align-items-center list-user-action">
                                <button
                                  className="btn btn-sm btn-icon btn-warning mx-1"
                                  data-toggle="tooltip"
                                  data-placement="top"
                                  title=""
                                  data-original-title="Edit"
                                  onClick={() => getCategoryItem(item)}
                                >
                                  <span className="btn-inner">
                                    <EditIcon />
                                  </span>
                                </button>

                                <button
                                  className="btn btn-sm btn-icon btn-danger mx-1"
                                  data-toggle="tooltip"
                                  data-placement="top"
                                  data-original-title="Delete"
                                  onClick={() => deleteCategory(item)}
                                >
                                  <span className="btn-inner">
                                    <DeleteIcon />
                                  </span>
                                </button>
                              </div>
                            </div>
                          </div>

                          <Confirmation
                            show={show}
                            handleClose={handleClose}
                            description="Are you sure you want to delete ?"
                            handleClick={confirmCategoryDelete}
                            yesText="Yes"
                            noText="No"
                          />
                        </td>

                        {item?.pid !== 0 ? (
                          <td>
                            <div className="col-md-12">
                              <div className="maindiv">
                                {/* <div
                                  className={
                                    item?.pstatus === "Active" ? "status" : ""
                                  }
                                /> */}
                                <div className="col-md-2">
                                  <span>
                                    <Image
                                      className="bg-soft-primary rounded img-fluid avatar-40 me-3"
                                      src={`${API_URL}/static/category/${item.pimage}`}
                                      alt="image"
                                    />
                                  </span>
                                </div>
                                <div className="col-md-5">
                                  <span className="cat-name">
                                    {item?.pname}
                                  </span>
                                </div>
                                <div className="col-md-1">
                                  <div className="flex align-items-center list-user-action">
                                    <button
                                      className="btn btn-sm btn-icon btn-warning mx-1"
                                      data-toggle="tooltip"
                                      data-placement="top"
                                      title=""
                                      data-original-title="Edit"
                                      onClick={() => getSubCategoryItem(item)}
                                    >
                                      <span className="btn-inner">
                                        <EditIcon />
                                      </span>
                                    </button>

                                    <button
                                      className="btn btn-sm btn-icon btn-danger mx-1"
                                      data-toggle="tooltip"
                                      data-placement="top"
                                      data-original-title="Delete"
                                      onClick={() => deleteSubCategory(item)}
                                    >
                                      <span className="btn-inner">
                                        <DeleteIcon />
                                      </span>
                                    </button>
                                  </div>
                                  <Confirmation
                                    show={showSubCategory}
                                    handleClose={handleSubCategoryClose}
                                    description="Are you sure you want to delete!"
                                    handleClick={confirmSubCategoryDelete}
                                    yesText="Yes"
                                    noText="No"
                                  />
                                </div>
                              </div>
                            </div>
                          </td>
                        ) : (
                          <td />
                        )}

                        {item?.cid !== 0 ? (
                          <td>
                            <div className="col-md-12">
                              <div className="maindiv">
                                {/* <div
                                  className={
                                    item?.cstatus === "Active" ? "status" : ""
                                  }
                                /> */}
                                <div className="col-md-2">
                                  <span>
                                    <Image
                                      className="bg-soft-primary rounded img-fluid avatar-40 me-3"
                                      src={`${API_URL}/static/category/${item.cimage}`}
                                      alt="image"
                                    />
                                  </span>
                                </div>
                                <div className="col-md-6">
                                  <span className="cat-name">
                                    {item?.cname}
                                  </span>
                                </div>
                                <div className="flex align-items-center list-user-action">
                                  <button
                                    className="btn btn-sm btn-icon btn-warning mx-1"
                                    data-toggle="tooltip"
                                    data-placement="top"
                                    title=""
                                    data-original-title="Edit"
                                    onClick={() => getItem(item)}
                                  >
                                    <span className="btn-inner">
                                      <EditIcon />
                                    </span>
                                  </button>

                                  <button
                                    className="btn btn-sm btn-icon btn-danger mx-1"
                                    data-toggle="tooltip"
                                    data-placement="top"
                                    data-original-title="Delete"
                                    onClick={() => deleteSubSubCategory(item)}
                                  >
                                    <span className="btn-inner">
                                      <DeleteIcon />
                                    </span>
                                  </button>
                                </div>

                                <Confirmation
                                  show={showSubSubCategory}
                                  handleClose={handleSubSubCategoryClose}
                                  description="Are you sure you want to delete!"
                                  handleClick={confirmSubSubCategoryDelete}
                                  yesText="Yes"
                                  noText="No"
                                />
                              </div>
                            </div>
                          </td>
                        ) : (
                          <td />
                        )}
                      </tr>
                    ))}

                    {props?.category?.error ? (
                      <span
                        style={{
                          color: "Red",
                          fontSize: "smaller",
                        }}
                      >
                        {props?.category?.error}
                      </span>
                    ) : null}
                  </tbody>
                </>
              )}
            </table>
          </div>
        </TableWrapper>
      </Col>

      <span className="col-md-6">
        Showing {(currentPage - 1) * showItems + 1} to{" "}
        {(currentPage - 1) * showItems + showItems > NUM_OF_RECORDS
          ? NUM_OF_RECORDS
          : showItems}{" "}
        of {NUM_OF_RECORDS} entries
      </span>

      <Pagination
        totalRecords={NUM_OF_RECORDS}
        pageLimit={showItems}
        onPageChanged={onPageChanged}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
      />
    </Row>
  );
}

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(category, dispatch) },
});

const mapStateToProps = (state) => ({
  category: state.category,
});

export default connect(mapStateToProps, mapDispatchToProps)(CategoryList);
