import { takeEvery, call, put } from "redux-saga/effects";
import { api, endPoints } from "../../utils/api";
import { actions } from "./bannerAction";

function* addBanner({ payload }) {
  try {
    const response = yield call(api.post, endPoints.ADD_BANNER, payload.body);
    yield put(actions.setAddBanner(response));
    payload.callback();
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getBanner() {
  try {
    const response = yield call(api.get, endPoints.GET_BANNER);
    yield put(actions.setBanner(response));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* deleteBanner({ payload }) {
  try {
    yield call(api.delete, endPoints.DELETE_BANNER, {
      urlParams: { id: payload },
    });
    yield put(actions.setDeleteBanner(payload));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* updateBanner({ payload }) {
  try {
    const response = yield call(
      api.put,
      endPoints.UPDATE_BANNER,
      payload.body.formData,
      { urlParams: { id: payload.body.params } }
    );

    yield put(actions.setUpdateBanner(response));
    payload.callback();
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getBannerById({ payload }) {
  try {
    const response = yield call(api.get, endPoints.GET_BANNER_BY_ID, {
      urlParams: { id: payload.id },
    });

    if (response) {
      yield put(actions.setBannerById(response.data.data));
    }
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

export function* bannerSaga() {
  yield takeEvery(actions.addBanner, addBanner);
  yield takeEvery(actions.getBanner, getBanner);
  yield takeEvery(actions.deleteBanner, deleteBanner);
  yield takeEvery(actions.updateBanner, updateBanner);
  yield takeEvery(actions.getBannerById, getBannerById);
}
