import { combineReducers } from "redux";
import auth from "./auth/authAction";
import user from "./user/userAction";
import category from "./category/categoryAction";
import item from "./item/itemAction";
import discount from "./discount/discountAction";
import banner from "./banner/bannerAction";
import promotion from "./promotion/promotionAction";
import order from "./order/orderAction";
import role from "./roles/roles-action";
import size from "./size/sizeAction";
import loyalitypoint from "./loyalitypoint/loyalitypointAction";
import newsletter from "./newsletter/newsletterAction";

const reducers = combineReducers({
  auth,
  user,
  category,
  item,
  discount,
  banner,
  promotion,
  order,
  role,
  size,
  loyalitypoint,
  newsletter,
});

export default reducers;
