import { createSlice } from "@reduxjs/toolkit";

export const initialState = {
  error: null,
  loading: false,
  newsletter: [],
  newsletterItem: [],
};

const newsletterReducer = createSlice({
  name: "newsletter",
  initialState,
  reducers: {
    addNewsLetter(state) {
      state.loading = true;
    },
    setAddNewsLetter(state, { payload }) {
      state.loading = false;
      state.newsletter = [
        ...(state.newsletter.newsletter || []),
        payload.data.data,
      ];
    },

    getNewsLetter(state) {
      state.loading = true;
    },
    setNewsLetter(state, { payload }) {
      state.loading = false;
      state.newsletter = payload.data.data;
    },

    getNewsLetterById(state) {
      state.loading = true;
    },
    setNewsLetterById(state, { payload }) {
      state.loading = false;
      state.newsletterItem = payload.data.data;
    },

    deleteNewsLetter(state) {
      state.loading = false;
    },
    setDeleteNewsLetter(state, { payload }) {
      state.loading = false;
      state.newsletter = state?.newsletter?.filter(
        (item) => item.id !== payload
      );
    },
    updateNewsLetter(state) {
      state.loading = true;
    },
    setUpdateNewsLetter(state, { payload }) {
      state.loading = false;
      state.newsletter = payload.data.newsletter;
    },
    getNewsLetterItemData(state) {
      state.loading = false;
    },
    setNewsLetterItemData(state, { payload }) {
      state.loading = false;
      state.newsletterItem = payload;
    },
    actionFalied(state, { payload }) {
      state.loading = false;
      state.error = payload?.response?.data?.message;
    },
  },
});

export const { name, actions } = newsletterReducer;
export default newsletterReducer.reducer;
