import { createSlice } from "@reduxjs/toolkit";

export const initialState = {
  error: null,
  loading: false,
  size: [],
  sizeItem: [],
};

const sizeReducer = createSlice({
  name: "size",
  initialState,
  reducers: {
    addSize(state) {
      state.loading = true;
    },
    setAddSize(state, { payload }) {
      state.loading = false;
      state.size = payload.data.size;
    },
    addSizePrice(state) {
      state.loading = true;
    },
    setAddSizePrice(state) {
      state.loading = false;
    },
    getSize(state) {
      state.loading = true;
    },
    setSize(state, { payload }) {
      state.loading = false;
      state.size = payload.data.data;
    },
    setDeleteSize(state) {
      state.loading = false;
    },
    deleteSize(state, { payload }) {
      state.size = state?.size?.filter((item) => item.id !== payload);
      state.loading = false;
    },
    getSizeById(state) {
      state.loading = true;
    },
    setSizeById(state, { payload }) {
      state.loading = false;
      state.sizeItem = payload.data.data;
    },
    updateSize(state) {
      state.loading = true;
    },
    setUpdateSize(state, { payload }) {
      state.loading = false;
      state.size = payload.data.size;
    },
    actionFalied(state, { payload }) {
      state.loading = false;
      state.error = payload?.response?.data?.message;
    },
  },
});

export const { name, actions } = sizeReducer;
export default sizeReducer.reducer;
