import React, { useCallback, useEffect, useState } from "react";
import { Col, Row } from "react-bootstrap";
import { bindActionCreators } from "redux";
import { useNavigate, useParams } from "react-router-dom";
import { connect } from "react-redux";
import { useFormik } from "formik";
import * as Yup from "yup";

import { actions as category } from "../../redux/category/categoryAction";
import { actions as discount } from "../../redux/discount/discountAction";
import FormikController from "../../components/controls/FormikController";
import Dropzone from "../../components/controls/fields/Dropzone";
import { actions as item } from "../../redux/item/itemAction";
import { actions as size } from "../../redux/size/sizeAction";
import { convertImgToBlob } from "../../shared/core-utils";
import { discountList } from "../../shared/contant";
import FormWrapper from "../../components/wrapper/form-wrapper";

const initialState = {
  name: "",
  description: "",
  discount_id: "",
  type: "",
  is_available: false,
  popular_pairing: false,
  defaultPrice: "",
  category_id: "",
  MediumPrice: "",
  isMultiplePrice: false,
};

// Commented code is for future refrence

const ManageItem = (props) => {
  const { mode } = props;

  const validationSchema = Yup.object({
    name: Yup.string().required().label("Product Name"),
    category_id: Yup.string().required().label("Category"),
    description: Yup.string().required().label("Description"),
    discount_id: Yup.number().required().label("Discount"),
    type: Yup.string().required("Required"),
    relavent_items: Yup.array(),
    isMultiplePrice: Yup.boolean(),
    defaultPrice: Yup.number()
      .typeError("Price must be number")
      .when("isMultiplePrice", {
        is: false,
        then: Yup.number().required("Default price is required"),
      }),
    MediumPrice: Yup.number()
      .typeError("Price must be number")
      .when("isMultiplePrice", {
        is: true,
        then: Yup.number().required("Medium price is required"),
      }),
  });

  const [initFormData, setInitFormData] = useState(initialState);
  const [images, setImages] = useState([]);
  // const [relaventProduct, setRelaventProduct] = useState([]);
  // const [relevantProductList, setRelaventProductList] = useState([]);
  const [productSizeList, setProductSizeList] = useState([]);
  const [isMultiple, setIsMultiple] = useState(false);
  const [discountTypeList, setDiscountTypeList] = useState([]);
  const [discountValueList, setDiscountValueList] = useState([]);

  const params = useParams();
  const navigate = useNavigate();
  const categoryList = props?.category?.category;

  const formik = useFormik({
    initialValues: initFormData,
    validationSchema: validationSchema,
    enableReinitialize: true,
    onSubmit: (values) => onSubmit(values),
  });

  const onDrop = (acceptedFiles) => {
    const acceptedImages = acceptedFiles.map((file) => {
      return Object.assign(file, {
        preview: URL.createObjectURL(file),
      });
    });
    setImages([...images, ...acceptedImages]);
  };

  const { handleChange, handleSubmit, values, resetForm, setFieldValue } =
    formik;

  useEffect(() => {
    props.actions.getCategory();
    props.actions.getSize();
    // props.actions.getRelavantProducts({ id: params.id ? params.id : "" });
    props.actions.getDiscountType();
    props.actions.getDiscount();
    if (params?.id) {
      props.actions.getItemByID({ id: params.id });
    }
  }, [props.actions, params.id]);

  useEffect(() => {
    setDiscountTypeList(
      props?.discount?.discountType.map(({ id, type }) => ({
        label: discountList[type],
        value: id,
      }))
    );

    const discountItem = props?.discount?.discountItem;

    if (discountItem) {
      const discountValues = props?.discount?.discount
        ?.filter(({ type_id }) => type_id === discountItem.type_id)
        ?.map(({ id, values }) => ({ label: values, value: id }));
      setDiscountValueList(discountValues);

      setFieldValue("discount_type_id", discountItem?.type_id);
      setFieldValue("discount_id", discountItem?.id);
    }
  }, [props.discount, setFieldValue]);

  useEffect(() => {
    const sizeList = (props?.size?.size || [])
      .filter(({ status }) => status === "Active")
      ?.map(({ id, size }) => ({ id, size }));
    setProductSizeList(sizeList);
  }, [props.size]);

  const setImage = useCallback(async () => {
    const imageToArray = JSON.parse(props?.item?.item?.images || "{}");

    const previewImages = await convertImgToBlob(imageToArray, "items");
    setImages([...previewImages]);
  }, [props?.item?.item?.images]);

  useEffect(() => {
    if (params.id && props?.mode === "Update") {
      const item = {};
      const fields = [
        "name",
        "description",
        "discount",
        "type",
        "category_id",
        "popular_pairing",
      ];

      const allPrices =
        props?.item?.item?.ItemSizePrices?.map(
          ({ ItemSize, price, size_id }) => ({
            size: ItemSize.size,
            price: price,
            id: size_id,
          })
        ) || [];

      if (allPrices.length) {
        allPrices.forEach(({ price, size }) => {
          item[`${size}Price`] = price;
          item[size] = true;
        });
        item["isMultiplePrice"] = true;
        setIsMultiple(true);
      } else {
        item["defaultPrice"] = props?.item?.item?.price;
      }

      fields.forEach((field) => {
        item[field] = props?.item?.item?.[field];
      });

      item["is_available"] = props?.item?.item?.status === "Active";

      setInitFormData({ ...initialState, ...item });

      setImage();
    }

    if (props?.item?.item?.discount_id) {
      props.actions.getDiscountById({ id: props?.item?.item?.discount_id });
    }

    // const existingRelevantProducts = props?.item?.item?.RelevantItems;
    // setRelaventProduct(
    //   existingRelevantProducts?.map(({ relevant_item }) => ({
    //     label: relevant_item?.name,
    //     value: relevant_item?.id,
    //   })) || []
    // );

    // const list =
    //   props?.item?.relevantProducts?.map(({ id, name }) => ({
    //     value: id,
    //     label: name,
    //   })) || [];

    // setRelaventProductList(list);
  }, [props?.item, params.id, props.mode, props.actions, setImage]);

  const onSubmit = (values) => {
    const fields = [
      "name",
      "description",
      "discount_id",
      "type",
      "category_id",
    ];

    const priceList = [];
    const formData = new FormData();

    if (values.isMultiplePrice) {
      productSizeList.forEach(({ size, id }) => {
        if (values[`${size}Price`] && values[size]) {
          priceList.push({ size_id: id, price: values[`${size}Price`] });
        }
      });
      formData.append("price", values?.MediumPrice);
    } else {
      formData.append("price", values?.defaultPrice);
    }

    fields.forEach((field) => {
      formData.append(field, values[field]);
    });

    images.forEach((image) => {
      const file = new File([image], image.name);
      formData.append(`files`, file);
    });
    // formData.append("is_popular", values.popular_pairing);
    formData.append("is_active", values.is_available);

    // const relevantProductIds = relaventProduct?.map(({ value }) => value);

    if (!params.id && mode === "Add") {
      props.actions.addItem({
        body: formData,
        callback: (id) => {
          if (isMultiple && priceList.length) {
            props.actions.addSizePrice({
              body: {
                item_id: id,
                sizeprices: priceList,
              },
            });
          }
          // if (relevantProductIds?.length) {
          //   props.actions.addRelaventProducts({
          //     body: {
          //       item_id: id,
          //       relevant_item_ids: relevantProductIds,
          //     },
          //   });
          // }
          navigate("/item/list");
          resetForm();
        },
      });
    } else {
      props.actions.updateItem({
        body: formData,
        id: params.id,
        callback: () => {
          props.actions.addSizePrice({
            body: {
              item_id: params.id,
              sizeprices: priceList,
            },
          });
          // if (relevantProductIds?.length) {
          //   props.actions.addRelaventProducts({
          //     body: {
          //       item_id: params.id,
          //       relevant_item_ids: relevantProductIds,
          //     },
          //   });
          // }
          navigate("/item/list");
          resetForm();
        },
      });
    }
  };

  const filteredCategory = categoryList
    ?.map(({ pid, gpid, cid, cname, pname, gpname }) => {
      let id = "";
      let name = "";
      if (gpid !== 0 && pid === 0 && cid === 0) {
        id = gpid;
        name = gpname;
      } else if (gpid !== 0 && pid !== 0 && cid === 0) {
        id = pid;
        name = pname;
      } else {
        id = cid;
        name = cname;
      }
      return { value: id, label: name };
    })
    .sort((a, b) => {
      var labelA = a.label.toUpperCase();
      var labelB = b.label.toUpperCase();
      return labelA < labelB ? -1 : labelA > labelB ? 1 : 0;
    });

  const handlePrice = (e) => {
    setFieldValue(e.target.name, e.target.checked);
    if (!e.target.checked) {
      setFieldValue(`${e.target.name}Price`, "");
    }
  };

  const handleDiscountList = (e) => {
    const discountValues = props?.discount?.discount
      ?.filter(({ type_id }) => type_id === parseInt(e.target.value))
      ?.map(({ id, values }) => ({ label: values, value: id }));
    setDiscountValueList(discountValues);
    setFieldValue("discount_type_id", e.target.value);
  };

  return (
    <Row>
      <Col xl="12" lg="9">
        <FormWrapper
          title={`${props?.mode === "Add" ? "Add" : "Update"} Category`}
          formik={formik}
          buttonTitle={mode === "Add" ? "Add Product" : "Update Product"}
          cancelClick={() => navigate("/item/list")}
          loading={props.item.loading}
          handleSubmit={handleSubmit}
        >
          <div className="row">
            <div className="col-6">
              <FormikController
                control="input"
                type="text"
                name="name"
                className="form-control"
                label="Product Name"
                handleChange={handleChange}
              />
            </div>
            <div className="col-6">
              <FormikController
                control="textarea"
                type="text"
                name="description"
                className="form-control"
                label="Description"
                onChange={handleChange}
              />
            </div>
          </div>

          <div className="row mt-4">
            <FormikController
              control="checkbox"
              name="isMultiplePrice"
              option="isMultiplePrice"
              label="Is Multiple price ?: "
              className="me-2 form-check-input"
              value={isMultiple}
              onChange={(e) => {
                setFieldValue("isMultiplePrice", e.target.checked);
                setIsMultiple(e.target.checked);
              }}
            />
            {isMultiple ? (
              <>
                <label style={{ fontWeight: "bolder" }}>Price by size:</label>
                {productSizeList?.map(({ size }) => {
                  return (
                    <div
                      key={size}
                      className="col-lg-6 col-md-12 mt-2 d-flex justify-content-start"
                    >
                      <div style={{ minWidth: "150px" }}>
                        <FormikController
                          control="checkbox"
                          name={size}
                          option={size}
                          label={`${size} : `}
                          className="me-2 form-check-input"
                          value={values[size]}
                          onChange={(e) => {
                            handlePrice(e);
                          }}
                        />
                      </div>
                      <div style={{ minWidth: "150px" }}>
                        <FormikController
                          control="input"
                          type="text"
                          name={`${size}Price`}
                          className="form-control"
                          label=""
                          disabled={!values[size]}
                          handleChange={handleChange}
                          style={{
                            borderRadius: "5px",
                            width: "30%",
                            display: "inline",
                            padding: "0px",
                          }}
                        />
                      </div>
                    </div>
                  );
                })}
              </>
            ) : (
              <div className="col-lg-6 col-md-12 mt-2">
                <FormikController
                  control="input"
                  type="text"
                  name="defaultPrice"
                  className="form-control"
                  label="Default price"
                  handleChange={handleChange}
                />
              </div>
            )}
          </div>

          <div className="row">
            <div className="col-6 mt-3">
              <FormikController
                // isControl={false}
                label="Discount Type"
                control="select"
                name="discount_type_id"
                handleChange={handleDiscountList}
                className="selectpicker form-control"
                options={discountTypeList}
                value={values.discount_type_id}
              />
            </div>
            <div className="col-6 mt-3">
              <FormikController
                label="Discount Value"
                control="select"
                name="discount_id"
                handleChange={handleChange}
                className="selectpicker form-control"
                options={discountValueList}
                value={values.discount_id}
              />
            </div>
          </div>
          <div className="row">
            <div className="col-6 mt-4">
              <Dropzone
                label="Upload Image"
                file={images}
                name="file"
                onDrop={onDrop}
                maxFiles={5}
                isMultiple={true}
                setImages={setImages}
              />
            </div>
            <div className="col-6 mt-4">
              <FormikController
                label="Category"
                control="select"
                name="category_id"
                handleChange={handleChange}
                className="selectpicker form-control"
                options={filteredCategory}
                value={values.category_id}
              />
            </div>
          </div>

          <div className="row">
            <div className="col-6">
              <FormikController
                control="radio"
                type="radio"
                name="type"
                label="Type"
                handleChange={handleChange}
                options={[
                  { value: "Veg", label: "Veg" },
                  { value: "Non-Veg", label: "Non-Veg" },
                ]}
              />
            </div>
            <div className="col-6">
              <div>
                <FormikController
                  control="checkbox"
                  name="is_available"
                  option="is_available"
                  label="Is available ?"
                  className="me-2 form-check-input"
                  value={values.is_available}
                  handleChange={handleChange}
                />
              </div>
              {/* <div className="ml-1">
                      <FormikController
                        control="checkbox"
                        name="popular_pairing"
                        option="popular_pairing"
                        label="Popular pairing ?"
                        className="me-2 form-check-input"
                        value={values.popular_pairing}
                        handleChange={handleChange}
                      />
                    </div> */}
            </div>
          </div>

          {/* <div className="row">
                  <div className="col-6">
                    <FormikController
                      control="multiselect"
                      name="relavent_items"
                      options={relevantProductList}
                      label="Relavent Items"
                      value={relaventProduct}
                      handleChange={(e) => setRelaventProduct(e)}
                    />
                  </div>
                </div> */}
        </FormWrapper>
      </Col>
    </Row>
  );
};

const mapDispatchToProps = (dispatch) => ({
  actions: {
    ...bindActionCreators(
      { ...category, ...item, ...size, ...discount },
      dispatch
    ),
  },
});

const mapStateToProps = (state) => ({
  category: state.category,
  item: state.item,
  size: state.size,
  discount: state.discount,
});

export default connect(mapStateToProps, mapDispatchToProps)(ManageItem);
