import React, { useCallback, useEffect, useState } from "react";
import { Col, Image, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import Pagination from "../pagination/pagination";
import { ReactComponent as EditIcon } from "../../assets/images/icons/Pen-Icon.svg";
import { ReactComponent as DeleteIcon } from "../../assets/images/icons/Bin-Icon.svg";
import { actions as item } from "../../redux/item/itemAction";
import { actions as category } from "../../redux/category/categoryAction";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import TableWrapper from "../../components/wrapper/table-wrapper";
import { API_URL } from "../../utils/api";
import Confirmation from "../../components/dialog/confirmation";
import FormikController from "../../components/controls/FormikController";

const ItemList = (props) => {
  const navigate = useNavigate();

  const [currentPage, setCurrentPage] = useState(1);
  const [showItems, setShowItems] = useState(10);
  const [id, setID] = useState();
  const [show, setShow] = useState(false);
  const [categoryID, setCategoryID] = useState();
  const handleClose = () => setShow(false);

  const [inputText, setInputText] = useState("");
  let inputHandler = (e) => {
    var lowerCase = e.target.value.toLowerCase();
    setInputText(lowerCase);
  };

  const categoryList = props?.category?.category;

  const filteredCategory = categoryList?.map(
    ({ pid, gpid, cid, cname, pname, gpname }) => {
      let id = "";
      let name = "";
      if (gpid !== 0 && pid === 0 && cid === 0) {
        id = gpid;
        name = gpname;
      } else if (gpid !== 0 && pid !== 0 && cid === 0) {
        id = pid;
        name = pname;
      } else {
        id = cid;
        name = cname;
      }
      return { value: id, label: name };
    }
  );

  const compare = (a, b) => {
    if (a.label.toLowerCase() < b.label.toLowerCase()) {
      return -1;
    }
  };

  filteredCategory.sort(compare);

  useEffect(() => {
    props.actions.getItemByCatID({ id: categoryID ? categoryID : "" });
    props.actions.getCategory();
  }, [props.actions, categoryID]);
  const itemList = props?.item?.itemList;

  const filteredData = itemList?.filter((el) => {
    if (inputText === "") {
      return el;
    } else {
      return (
        el?.name?.toLowerCase().includes(inputText) ||
        el?.type?.toLowerCase().includes(inputText) ||
        el?.price?.toString().toLowerCase().includes(inputText)
      );
    }
  });

  let NUM_OF_RECORDS = filteredData?.length;

  const onPageChanged = useCallback(
    (event, page) => {
      event.preventDefault();
      setCurrentPage(page);
    },
    [setCurrentPage]
  );

  const currentData = filteredData?.slice(
    (currentPage - 1) * showItems,
    (currentPage - 1) * showItems + showItems
  );

  const handleShowItemChange = (e) => {
    setShowItems(e.target.value);
  };

  const handleItemGet = (item) => {
    navigate(`/item/update/${item.id}`);
  };

  const confirmItemDelete = (id) => {
    props.actions.deleteItem({ id: id });
    setShow(false);
  };

  const deleteItem = ({ id }) => {
    setID(id);
    setShow(true);
  };

  return (
    <Row>
      <Col sm="12">
        <div className="row mb-3 col-md-3">
          <FormikController
            label="Category"
            control="select"
            name="role_type"
            isControl={false}
            handleChange={(e) => setCategoryID(e.target.value)}
            className="selectpicker form-control ms-2"
            style={{ lineHeight: "1" }}
            options={[{ value: "", label: "All items" }, ...filteredCategory]}
          />
        </div>

        <TableWrapper
          title="Item List"
          buttonTitle="Add Item"
          buttonAction={(e) => navigate("/item/add")}
          handleShowItemChange={handleShowItemChange}
          inputHandler={inputHandler}
        >
          <div className="table-responsive">
            <table
              id="user-list-table"
              className="table table-striped"
              role="grid"
              data-toggle="data-table"
            >
              {filteredData?.length === 0 ? (
                <tbody>
                  <tr
                    style={{
                      display: "flex",
                      justifyContent: "center",
                    }}
                  >
                    <td>
                      <h3>No Record Found</h3>
                    </td>
                  </tr>
                </tbody>
              ) : (
                <>
                  <thead>
                    <tr className="ligth">
                      <th className="text-center">Profile</th>
                      <th className="text-center">Name</th>
                      <th className="text-center">Price</th>
                      <th className="text-center">Type</th>
                      <th className="text-center">Status</th>
                      <th className="text-center" min-width="100px">
                        Action
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    {currentData?.map((item, idx) => (
                      <tr key={idx}>
                        <td className="text-center">
                          <Image
                            className="bg-soft-primary rounded img-fluid avatar-40 me-3"
                            src={`${API_URL}/static/items/${
                              JSON.parse(item.images)?.[0]?.filename
                            }`}
                            alt="profile"
                          />
                        </td>
                        <td className="text-center">{item?.name}</td>
                        <td className="text-center">{item?.price}</td>
                        <td className="text-center">{item?.type}</td>
                        <td className="text-center">
                          <div className="maindiv">
                            <div className="col-md-6">
                              {item.status === "Active" ? (
                                <span
                                  className="badge"
                                  style={{
                                    backgroundColor: "green",
                                  }}
                                >
                                  {item.status}
                                </span>
                              ) : (
                                <span
                                  className="badge"
                                  style={{
                                    backgroundColor: "red",
                                  }}
                                >
                                  {item.status}
                                </span>
                              )}
                            </div>
                          </div>
                        </td>
                        <td className="text-center">
                          <div className="flex align-items-center list-user-action">
                            <button
                              className="btn btn-sm btn-icon btn-warning mx-1"
                              data-toggle="tooltip"
                              data-placement="top"
                              title=""
                              data-original-title="Edit"
                              onClick={() => handleItemGet(item)}
                            >
                              <span className="btn-inner">
                                <EditIcon />
                              </span>
                            </button>

                            <button
                              className="btn btn-sm btn-icon btn-danger mx-1"
                              data-toggle="tooltip"
                              data-placement="top"
                              onClick={() => deleteItem(item)}
                            >
                              <DeleteIcon />
                            </button>
                          </div>
                          <Confirmation
                            show={show}
                            handleClose={handleClose}
                            description="Are you sure you want to delete ?"
                            handleClick={() => confirmItemDelete(id)}
                            yesText="Yes"
                            noText="No"
                          />
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </>
              )}
            </table>
          </div>
        </TableWrapper>
      </Col>

      <span className="col-md-6">
        Showing {(currentPage - 1) * showItems + 1} to{" "}
        {(currentPage - 1) * showItems + showItems > NUM_OF_RECORDS
          ? NUM_OF_RECORDS
          : showItems}{" "}
        of {NUM_OF_RECORDS} entries
      </span>

      <Pagination
        totalRecords={NUM_OF_RECORDS}
        pageLimit={showItems}
        onPageChanged={onPageChanged}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
      />
    </Row>
  );
};

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators({ ...item, ...category }, dispatch) },
});

const mapStateToProps = (state) => ({
  item: state.item,
  category: state.category,
});

export default connect(mapStateToProps, mapDispatchToProps)(ItemList);
