import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Row, Col } from "react-bootstrap";
import TableWrapper from "../../components/wrapper/table-wrapper";
import { ReactComponent as EditIcon } from "../../assets/images/icons/Pen-Icon.svg";
import { ReactComponent as DeleteIcon } from "../../assets/images/icons/Bin-Icon.svg";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { actions as size } from "../../redux/size/sizeAction";
import Confirmation from "../../components/dialog/confirmation";

function SizeList(props) {
  const navigate = useNavigate();

  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const [id, setID] = useState();
  const [inputText, setInputText] = useState("");

  const sizeList = props?.size?.size;

  let inputHandler = (e) => {
    var lowerCase = e.target.value.toLowerCase();
    setInputText(lowerCase);
  };

  const filteredData = sizeList?.filter((el) => {
    if (inputText === "") {
      return el;
    } else {
      return el?.size?.toLowerCase().includes(inputText);
    }
  });

  const handleItemGet = (item) => {
    navigate(`/size/update/${item.id}`);
  };

  const handleDelete = (item) => (e) => {
    setID(item.id);
    e.preventDefault();
    setShow(true);
  };

  const confirmDelete = (id) => {
    props.actions.deleteSize(id);
    setShow(false);
  };

  useEffect(() => {
    props.actions.getSize();
  }, [props.actions]);

  return (
    <div>
      <Row>
        <Col sm="12">
          <TableWrapper
            title="Size List"
            buttonTitle="Add Size"
            buttonAction={() => navigate("/size/add")}
            inputHandler={inputHandler}
          >
            <div className="table-responsive">
              <table
                id="user-list-table"
                className="table table-striped"
                role="grid"
                data-toggle="data-table"
              >
                {filteredData?.length === 0 ? (
                  <tbody>
                    <tr
                      style={{
                        display: "flex",
                        justifyContent: "center",
                      }}
                    >
                      <td>
                        <h3>No Record Found</h3>
                      </td>
                    </tr>
                  </tbody>
                ) : (
                  <>
                    <thead>
                      <tr className="ligth">
                        <th className="text-center">Size</th>
                        <th className="text-center">Status</th>
                        <th min-width="100px" className="text-center">
                          Action
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      {filteredData?.map((item, idx) => (
                        <tr key={idx}>
                          <td className="text-center">{item.size}</td>
                          <td className="text-center">
                            {item.status === "Active" ? (
                              <span
                                className="badge"
                                style={{ backgroundColor: "green" }}
                              >
                                {item.status}
                              </span>
                            ) : (
                              <span
                                className="badge"
                                style={{ backgroundColor: "red" }}
                              >
                                {item.status}
                              </span>
                            )}
                          </td>
                          <td className="text-center">
                            <div className="flex align-items-center list-user-action">
                              <button
                                className="btn btn-sm btn-icon btn-warning"
                                data-toggle="tooltip"
                                data-placement="top"
                                title=""
                                data-original-title="Edit"
                                onClick={() => handleItemGet(item)}
                              >
                                <span className="btn-inner">
                                  <EditIcon />
                                </span>
                              </button>
                              <button
                                className="btn btn-sm btn-icon btn-danger"
                                data-toggle="tooltip"
                                data-placement="top"
                                data-original-title="Delete"
                                onClick={handleDelete(item)}
                              >
                                <span className="btn-inner">
                                  <DeleteIcon />
                                </span>
                              </button>

                              <Confirmation
                                show={show}
                                handleClose={handleClose}
                                description="Are you sure you want to delete ?"
                                handleClick={() => confirmDelete(id)}
                                yesText="Yes"
                                noText="No"
                              />
                            </div>
                          </td>
                        </tr>
                      ))}
                      {props?.auth?.error ? (
                        <span
                          style={{
                            color: "Red",
                            fontSize: "smaller",
                          }}
                        >
                          {props?.auth?.error}
                        </span>
                      ) : null}
                    </tbody>
                  </>
                )}
              </table>
            </div>
          </TableWrapper>
        </Col>
      </Row>
    </div>
  );
}

const mapDispatchToProps = (dispatch) => ({
  actions: { ...bindActionCreators(size, dispatch) },
});

const mapStateToProps = (state) => ({
  size: state.size,
});

export default connect(mapStateToProps, mapDispatchToProps)(SizeList);
