import { takeEvery, call, put } from "redux-saga/effects";
import { api, endPoints } from "../../utils/api";
import { actions } from "./categoryAction";

function* addCategory({ payload }) {
  try {
    const response = yield call(api.post, endPoints.ADD_CATEGORY, payload.body);
    yield put(actions.setAddCategory(response));
    yield call(payload.callback);
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getCategory() {
  try {
    const response = yield call(api.get, endPoints.GET_CATEGORY);
    yield put(actions.setCategory(response));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* deleteCategory({ payload }) {
  try {
    yield call(api.delete, endPoints.DELETE_CATEGORY, {
      urlParams: { id: payload.item },
    });
    yield put(actions.setDeleteCategory(payload));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getCategoryById({ payload }) {
  try {
    const response = yield call(api.get, endPoints.CATEGORY_BY_ID, {
      urlParams: {
        id: payload.id,
      },
    });
    yield put(actions.setCategoryById(response));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* updateCategory({ payload }) {
  try {
    const response = yield call(
      api.put,
      endPoints.UPDATE_CATEGORY,
      payload.body.formData,
      { urlParams: { id: payload.body.params } }
    );

    yield put(actions.setUpdateCategory(response));
    yield call(payload.callback);
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

function* getCategoryItemData({ payload }) {
  try {
    yield put(actions.setCategoryItemData(payload));
  } catch (error) {
    yield put(actions.actionFalied(error));
  }
}

export function* categorySaga() {
  yield takeEvery(actions.addCategory, addCategory);
  yield takeEvery(actions.getCategory, getCategory);
  yield takeEvery(actions.deleteCategory, deleteCategory);
  yield takeEvery(actions.getCategoryById, getCategoryById);
  yield takeEvery(actions.updateCategory, updateCategory);
  yield takeEvery(actions.getCategoryItemData, getCategoryItemData);
}
